module.exports = [{"story":0,"bookId":40,"bookName":"Matthew","prevPath":"/","path":"/300","nextPath":"/301","display":"none","title":"Introduction to Matthew"},{"story":1,"bookId":40,"bookName":"Matthew","ref":"1:1-2:23","prevPath":"/300","path":"/301","nextPath":"/302","display":"block","title":"The King's Amazing Birth","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this story ? Why?","Question2":"What strengths and/or weaknesses do you see in this character?","Question3":"What do you think God was wanting your selected character to learn?","Question4":"What do you think God is wanting you to learn through this story? How can you cooperate with his good purposes today?"},{"story":2,"bookId":40,"bookName":"Matthew","ref":"3:1-4:25","prevPath":"/301","path":"/302","nextPath":"/303","display":"block","title":"The Kingdom Introduced","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":3,"bookId":40,"bookName":"Matthew","ref":"5:1-7:29","prevPath":"/302","path":"/303","nextPath":"/304","display":"block","title":"Principles For Kingdom Living","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":4,"bookId":40,"bookName":"Matthew","ref":"8:1-9:35","prevPath":"/303","path":"/304","nextPath":"/305","display":"block","title":"The King Acts in Response To Faith","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?"},{"story":5,"bookId":40,"bookName":"Matthew","ref":"9:36-11:1","prevPath":"/304","path":"/305","nextPath":"/306","display":"block","title":"Prepared To Extend the Kingdom","questionsTitle":"DIGGING DEEPER","Question1":"What did Jesus instruct his disciples to do?","Question2":"What did Jesus say about how the disciples will be treated?","Question3":"How do you think the disciples felt about Jesus' instructions?","Question4":"In what ways do Jesus' instructions apply to you?"},{"story":6,"bookId":40,"bookName":"Matthew","ref":"11:2-12:50","prevPath":"/305","path":"/306","nextPath":"/307","display":"block","title":"The King Acts Provoking Opposition","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?"},{"story":7,"bookId":40,"bookName":"Matthew","ref":"13:1-13:53","prevPath":"/306","path":"/307","nextPath":"/308","display":"block","title":"Stories Describing the Kingdom","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you describe any comparisons made by the author of this portion of Scripture? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you describe any contrasts made by the author of this story portion of Scripture? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have observed in this portion of Scripture.","Question4":"Please take time to post on your social media what you have learned through this passage."},{"story":8,"bookId":40,"bookName":"Matthew","ref":"13:54-17:27","prevPath":"/307","path":"/308","nextPath":"/309","display":"block","title":"The King Acts: Faith & Opposition","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":9,"bookId":40,"bookName":"Matthew","ref":"18:1-19:1a","prevPath":"/308","path":"/309","nextPath":"/310","display":"block","title":"Practical Values of the Kingdom","questionsTitle":"DIGGING DEEPER","Question1":"What does Jesus teach about forgiveness?","Question2":"According to this passage how should we go about restoring broken relationships? ","Question3":"Is there someone whom you have wronged and you need to ask forgiveness from? Make specific plans to do that.","Question4":"Is there someone you need to forgive in your life? Talk to God about it."},{"story":10,"bookId":40,"bookName":"Matthew","ref":"19:1b-20:34","prevPath":"/309","path":"/310","nextPath":"/311","display":"block","title":"The King Approaches Jerusalem","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":11,"bookId":40,"bookName":"Matthew","ref":"21:1-22:46","prevPath":"/310","path":"/311","nextPath":"/312","display":"block","title":"The King Enters Jerusalem","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this story ? Why?","Question2":"What strengths and/or weaknesses do you see in this character?","Question3":"What do you think God was wanting your selected character to learn?","Question4":"What do you think God is wanting you to learn through this story? How can you cooperate with his good purposes today?"},{"story":12,"bookId":40,"bookName":"Matthew","ref":"23:1-23:39","prevPath":"/311","path":"/312","nextPath":"/313","display":"block","title":"The King Corrects & Laments","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you see any comparisons made by the author of this passage? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you see, describe any contrasts made by the author of this passage? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have noted in this passage.","Question4":"Please take time to tell a friend what God is teaching you through this reflection on the Bible."},{"story":13,"bookId":40,"bookName":"Matthew","ref":"24:1-25:46","prevPath":"/312","path":"/313","nextPath":"/314","display":"block","title":"The King Warns & Encourages","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":14,"bookId":40,"bookName":"Matthew","ref":"26:1-27:26","prevPath":"/313","path":"/314","nextPath":"/315","display":"block","title":"The King's Final Days","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?"},{"story":15,"bookId":40,"bookName":"Matthew","ref":"27:27-28:20","prevPath":"/314","path":"/315","nextPath":"/316","display":"block","title":"The King Dead & Alive","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?"},{"story":16,"bookId":41,"bookName":"Mark","prevPath":"/315","path":"/316","nextPath":"/317","display":"none","title":"Introduction to Mark","html":"<h6>Good news for people in crisis</h6><p>The humility and power of Jesus inspires faith and endurance</p><h6>How to read Mark</h6><p>All-news radio and TV stations capture the highlights of the worlds news in thirty minutes. Marks gospel offers a similar fast-paced report on the story of Jesus Christ, the Son of God. Out of obscurity, this unique God-man explodes into the headlines as he preaches, performs miracles, and encounters both great popularity and deadly opposition. It is the greatest news story of all time.</p><p>Written by a young man who worked alongside the fisherman who had lived the story, this account is strong on action and wastes no extra words, giving us a vivid, firsthand portrayal of Jesus. His actions are center stage, as he came not to be served, but to serve, and to give his life as a ransom for many (Mar 10:45). We see him hard at work, helping people in need, healing those who were sick, encouraging those who were without hope, setting free those who were in bondage, enlightening those who were in darkness.</p><p>Marks action-packed account of Jesus life culminates by immersing us in details of the suffering and sacrifice of his final week on earth. These powerful stories were written to strengthen the Roman believers suffering persecution, to both live and die fearlessly. Consider the implications of following the example of the supreme Servant, who calls you to a ministry of servanthood.</p><h6>Who wrote this book?</h6><p>John Mark, the son of a Jerusalem widow whose home was a meeting place for early believers (see Acts 12:12). Mark served as Peters translator, so he most likely recorded the events as he heard them firsthand. So, though Mark wrote the words, it could be said that this book is the gospel according to Peter.</p><h6>When was it written?</h6><p>This account was probably written during the terrible persecution of Emperor Nero, close to the time of Peters execution. This would be after the burning of Rome in July, AD 64 but before the fall of Jerusalem to Roman armies in August, AD 70.</p><h6>To whom was it written and why?</h6><p>The books distinctly non-Jewish flavor and notable allusions to Roman customs make it clear that it was written for believers in Rome. The Roman Empire, the dominant world power, had begun to persecute Christians. Mark wanted to encourage these suffering believers.</p><h6>SourceView Insights</h6><p>The overall breakdown of this book shows us that about 53 percent of Mark are the words of The Narrator (black); 35 percent are Jesus words (red); 3 percent are the disciples (green); and 9 percent are all others (blue). Clearly Jesus is the central person in this book. He speaks about 156 times compared to almost 40 different green speaking parts and more than a 100 distinct blue passages. And yet, this book which conveys Peters testimony of Jesus life and ministry, highlights the experiences of the disciples in a special way. As a group, the disciples intervene about 19 times. Simon Peter has the lead role among them, speaking nine times. Other disciples who have a speaking part are John, James, Andrew, and Judas Iscariot.</p><p>Peters words focus on who Jesus is. The very first time he speaks he says to Jesus, You are the Christ. (8:29). It seems like hes really got it, but in his longest speech Peter shows that he still doesnt grasp the uniqueness of Jesus. On the Mount of Transfiguration he says that they will make three tents: one for you, one for Moses, and one for Elijah (9:5). Then things get even worse. The last two times he speaks he denies who Jesus is and any relationship with him (14:68; 14:71). Through his story Peter thus identifies himself with other believers who sometimes are certain in their faith, sometimes confused about whats central, and at other times afraid of identifying with Jesus. This must have been pastorally comforting for Marks original hearers going through Neros great persecution.</p><p>Who is Jesus? Amazed by his displays of power, Jesus disciples ask, Who then is this, that even the wind and the sea obey him? (4:41). But this question is not only raised by Jesus followers. It is also posed both by the top religious (14:61) and political authorities in Jerusalem (15:2). But Marks gospel leaves the reader with no doubt as to who Jesus really is. God speaks only twice and both times affirms that Jesus is the dearly loved Son. Neither is there any doubt when demons speak. On every occasion that they have a speaking part they affirm Jesus divinity (1:2; 3:11; 5:7).</p><p>All of this is set within the bookmarks of the first and last blue sections of Mark spoken by humans. The book opens with the affirmation that Jesus is mightier than the most devout of religious leaders (1:7). The books ends with an amazing confession from an unexpected source: Truly this man was the Son of God! (15:39).</p><p>Faced with who Jesus is, Marks gospel calls his audience to faith. SourceView enables us to discover that the individual with the largest speaking role outside of the disciples is the father of a very sick boy whose life hung in the balance. In this life and death crisis, the father cries out to Jesus, I believe. Help my unbelief! (9:24). Many of Marks original readers, facing their own life and death crises because of the Roman persecution, would have identified with this father and taken courage to pray a similar prayer. For this very reason, Mark includes much teaching from Jesus on what it means to believe and have faith in God (reflect on 1:15; 4:13-20; 4:40; 5:34; 9:19; 9:23b; 9:39-50; 10:52; 11:22-25; 16:15-18).</p>"},{"story":17,"bookId":41,"bookName":"Mark","ref":"1:1-3:6","prevPath":"/316","path":"/317","nextPath":"/318","display":"block","title":"The Good News of Jesus Christ","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List the various questions raised in this portion of Scripture. Who makes them? To whom are they made?","Question2":"Have you ever asked any questions like these? When? To whom?","Question3":"Does this portion of Scripture raise any fresh questions in your own heart and mind? What are they?","Question4":"Write a trusted mentor with a question you may have about this portion of Scripture."},{"story":18,"bookId":41,"bookName":"Mark","ref":"3:7-5:43","prevPath":"/317","path":"/318","nextPath":"/319","display":"block","title":"Serving in Deed & Word","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this story? Why?","Question2":"What strengths and/or weaknesses do you see in this character?","Question3":"What do you think God was wanting your selected character to learn?","Question4":"What do you think God is wanting you to learn through this story? How can you cooperate with his good purposes today?"},{"story":19,"bookId":41,"bookName":"Mark","ref":"6:1-7:37","prevPath":"/318","path":"/319","nextPath":"/320","display":"block","title":"Power, Servanthood & Persecution","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":20,"bookId":41,"bookName":"Mark","ref":"8:1-9:50","prevPath":"/319","path":"/320","nextPath":"/321","display":"block","title":"Jesus is the Christ","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Meditate on the acts of God described in this portion of Scripture.","Question2":"What do these clarify about the heart of God?","Question3":"Have you experienced this expression of the heart of God in your life? If yes, how so? If not, what steps can you take to experience this?","Question4":"What can you do to grow in your desire to know and obey God more intimately in your life?"},{"story":21,"bookId":41,"bookName":"Mark","ref":"10:1-11:26","prevPath":"/320","path":"/321","nextPath":"/322","display":"block","title":"The Christ Came to Serve","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?"},{"story":22,"bookId":41,"bookName":"Mark","ref":"11:27-13:37","prevPath":"/321","path":"/322","nextPath":"/323","display":"block","title":"Challenged By Religious Leaders","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given guidance can you observe in this passage?","Question2":"Put yourself in the passage and envision what it might have been like to receive this guidance. How does this relate to you and your family today?","Question3":"What God-inspired encouragement can you observe in this passage? How can you experience more of this in your own life?","Question4":"Write a poem about what this encouragement means for you."},{"story":23,"bookId":41,"bookName":"Mark","ref":"14:1-14:72","prevPath":"/322","path":"/323","nextPath":"/324","display":"block","title":"Betrayed, Arrested & Denied","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List the various questions raised in this portion of Scripture. Who makes them? To whom are they made?","Question2":"Have you ever asked any questions like these? When? To whom?","Question3":"Does this portion of Scripture raise any fresh questions in your own heart and mind? What are they?","Question4":"Write a trusted mentor with a question you may have about this portion of Scripture."},{"story":24,"bookId":41,"bookName":"Mark","ref":"15:1-16:20","prevPath":"/323","path":"/324","nextPath":"/325","display":"block","title":"Tried, Crucified & Risen","questionsTitle":"DIGGING DEEPER","Question1":"Imagine yourself in the story, observing first hand the crucifixion and resurrection of Jesus. With which character do you most identify? Why?","Question2":"Note the different individual and groups that encountered the risen Jesus. How did each respond?","Question3":"When Jesus appeared to his disciples what did he do and say to them?","Question4":"How has Jesus' final command made a difference in your life? How should it?"},{"story":25,"bookId":42,"bookName":"Luke","prevPath":"/324","path":"/325","nextPath":"/326","display":"none","title":"Introduction to Luke","html":"<h6>A Savior for all people</h6><p>The pivotal event of historyGod steps in to seek and save the lost</p><h6>How to read Luke</h6><p>Each gospel has its own particular flavor as each author portrays unique aspects of Jesus life and ministry. Luke, one of Pauls disciples, was a non-Jewish physician from the Roman province of Macedonia. He did not experience the gospel events first-hand, but carefully researched the facts from those who had been eye witnesses (Luk 1:1-3). His keen investigation brings to light some priceless stories that are not told in the other gospel accounts. Luke wanted to be sure he told the full story, including rich details of Jesus birth, life, death, and resurrection.</p><p>Lukes is a very personal gospel. We observe many individuals meeting one-on-one with Jesus in transformational encounterspeople who were often marginalized and cast off by religious society: women, foreigners, the sick, the lost, the broken, the poor, the needy, the suffering, the powerless, and the despised. Only Luke records the parable of the Good Samaritan. Only Luke includes three more parables and places them at the heart of his story, in chapter 15. They are the parables of a lost coin, a lost sheep, and a lost son. All three highlight the central message of the book that can best be summed up in the words of Jesus: For the Son of Man came to seek and to save that which was lost (Luk 19:10).</p><h6>Who wrote this book and when?</h6><p>Luke, a Gentile medical doctor and a companion of Paul the apostle, probably wrote the gospel between AD 59 and 63. He also wrote the New Testament book of Acts. This two volume work follows an interesting geographical organization:</p><br /><table style='max-width:500px; margin: 0 auto; font-size: 13px'> <tr> <th style='width:35%'>Reference</th> <th>Geographical Organization</th> </tr><tr> <th>Luke 1:1-4:13</th> <th>Jesus birth, childhood, and baptism</th> </tr><tr> <th>Luke 4:14-9:50</th> <th>Jesus ministry in Galilee and beyond</th> </tr><tr> <th>Luke 9:51-19:35</th> <th>Jesus ministry en route to Jerusalem (note Luk 9:53; 10:38; 13:22; 17:11; 18:31; 19:11, 28)</th> </tr><tr> <th>Luke 19:36-24:53</th> <th>Jesus ministry in Jerusalem</th> </tr><tr> <th>Acts 1:1-7:60</th> <th>The churchs ministry in Jerusalem</th> </tr><tr> <th>Acts 8:1-12:25</th> <th>The churchs ministry in all of Judea and Samaria</th> </tr><tr> <th>Acts 13:1-28:31</th> <th>The churchs ministry to the ends of the earth (see Act 1:8)</th> </tr></table><br /><h6>To whom was it written and why?</h6><p>Luke wrote to Theophilus, probably a Gentile who was either a new believer or someone seeking to learn about Jesus. Theophilus means lover of God, leading some to think the book was written to people who loved God. Luke hoped Theophilus and other readers would learn that Gods love reaches out to enfold people from all nationsJews and Gentiles alike.</p><h6>SourceView Insights</h6><p>The total supporting cast (blue text) found in the four gospels comprises 99 different individuals or groups. Eight of them are found in all four gospels. They are the indispensable players in the story of Jesus. They include: John the Baptist, The Crowd, The Pharisees, Jewish Leaders, Pontius Pilate, Bystanders at the Trial, A Servant Girl, and Roman Soldiers.</p><p>Then there are 32 cast members who speak in multiple gospels but not in all of them. They include eleven who speak in the first three gospels; one who has a voice in all but Luke, and one in all but Mark. In addition six speak only in Matthew and Mark; four in Matthew and Luke; four in Mark and Luke; one in Mark and John; and four in Luke and John. Can you discover them all?</p><p>This leaves 59 cast members, each of whom has a speaking part in only one gospel account. Reflection on the unique contribution of each of these helps the reader discover the distinctive focus of each gospel story. Of these 30 are found in three gospels (7 in Matthew, 5 in Mark, and 18 in John). Can you discover who they are and reflect on why each author decided to include their words into their gospel narrative? That means that nearly half of these unique players (28 to be precise!) find their voice only in the gospel of Luke. They include:</p><br /><ul><li>Cleopas</li><li>Cleopas &amp; Cleopas Companion</li><li>A Dinner Guest</li><li>Elizabeth</li><li>Elizabeths Neighbors</li><li>Gabriel</li><li>A Host of Angels</li><li>One of Ten Lepers</li><li>The Owners of the Colt</li><li>People in Capernaum</li><li>People in Judea</li><li>People in Nain</li><li>The Pharisees &amp; the Religious Leaders</li><li>The Repentant Thief</li><li>A Roman Officers Friends</li><li>Shepherds near Bethlehem</li><li>Simeon</li><li>Simon the Pharisee</li><li>Some Elders</li><li>Some in Simons Home</li><li>Someone Along the Road</li><li>Someone from the Crowd</li><li>A Synagogue Leader</li><li>Ten Lepers</li><li>The Unrepentant Thief</li><li>A Woman in the Crowd</li><li>Zacchaeus</li><li>Zechariah</li></ul><br /><p>Reading the words of these members of the cast will help you discover the special perspective that Luke brings to the gospel story. Seek to uncover other passages that are unique to Luke not found in the other three gospels. Then take time to meditate on why Luke included them in his narrative. You might discover little treasures by focusing on these unique passages, as illustrated below:<br /><br /><table style='font-size: 15px; max-width: 800px; margin: 0 auto'> <tr> <th colspan='2' style='text-align:center'>Johns Birth Foretold<br />(Luke 1:5-25)</th> <th colspan='2' style='text-align:center'>Jesus Birth Foretold<br />(Luke 1:26-56)</th> </tr><tr> <th>1:11</th> <th>Gabriel sent</th> <th>1:26</th> <th>Gabriel sent</th> </tr><tr> <th>1:13</th> <th>Elizabeth will bear a son</th> <th>1:31</th> <th>Mary will give birth to a son</th> </tr><tr> <th>1:17</th> <th>to make ready a people</th> <th>1:33</th> <th>to reign over the house of Jacob</th> </tr><tr> <th>1:18</th> <th>How can this be? My wife is old</th> <th>1:34</th> <th>How will this be? I am a virgin</th> </tr><tr> <th>1:20</th> <th>Because you did not believe</th> <th>1:45</th> <th>Blessed is she who has believed</th> </tr><tr> <th>1:22</th> <th>Zechariah unable to speak</th> <th>1:46</th> <th>Mary glorifies the Lord</th> </tr></table><p>"},{"story":26,"bookId":42,"bookName":"Luke","ref":"1:1-2:40","prevPath":"/325","path":"/326","nextPath":"/327","display":"block","title":"Two Miraculous Births","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you discover any comparisons made by the author of this story? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you discover any contrasts made by the author of this story? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have found in this story.","Question4":"Please take time to write a short prayer to God responding to what you have discovered through these comparisons, contrasts and repetitions."},{"story":27,"bookId":42,"bookName":"Luke","ref":"2:41-4:44","prevPath":"/326","path":"/327","nextPath":"/328","display":"block","title":"Preparing for the Mission in Galilee","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?"},{"story":28,"bookId":42,"bookName":"Luke","ref":"5:1-6:49","prevPath":"/327","path":"/328","nextPath":"/329","display":"block","title":"Calling, Healing & Teaching","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":29,"bookId":42,"bookName":"Luke","ref":"7:1-8:56","prevPath":"/328","path":"/329","nextPath":"/330","display":"block","title":"Power, Miracles & Faith","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you discover any comparisons made by the author of this story? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you discover any contrasts made by the author of this story? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have found in this story.","Question4":"Please take time to write a short prayer to God responding to what you have discovered through these comparisons, contrasts and repetitions."},{"story":30,"bookId":42,"bookName":"Luke","ref":"9:1-9:50","prevPath":"/329","path":"/330","nextPath":"/331","display":"block","title":"The Mission in Galilee Completed","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this passage? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this passage?","Question3":"What do you think God was wanting do in the life of your selected character?","Question4":"What do you think God is wanting to do in your life as you reflect on this passage? How can you cooperate with his loving purposes this week?"},{"story":31,"bookId":42,"bookName":"Luke","ref":"9:51-11:54","prevPath":"/330","path":"/331","nextPath":"/332","display":"block","title":"Jesus Set His Face to Jerusalem","questionsTitle":"DIGGING DEEPER","Question1":"Note the different ways people responded to Jesus as he journeyed to Jerusalem.","Question2":"Do you identify with one of those responses? If so, how?","Question3":"According to Jesus, who is one's \"neighbor\" and what does loving them look like?","Question4":"The disciples asked Jesus to teach them to pray. What can you learn from Jesus' response?"},{"story":32,"bookId":42,"bookName":"Luke","ref":"12:1-13:9","prevPath":"/331","path":"/332","nextPath":"/333","display":"block","title":"Teaching on True Discipleship","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List the various questions raised in this portion of Scripture. Who makes them? To whom are they made?","Question2":"Have you ever asked any questions like these? When? To whom?","Question3":"Does this portion of Scripture raise any fresh questions in your own heart and mind? What are they?","Question4":"Write a trusted mentor with a question you may have about this portion of Scripture."},{"story":33,"bookId":42,"bookName":"Luke","ref":"13:10-15:32","prevPath":"/332","path":"/333","nextPath":"/334","display":"block","title":"Power & Parables","questionsTitle":"OPENING THE WORD","Question1":"What is your favorite part of this story?","Question2":"What do you learn about God?","Question3":"What do you learn about human beings?","Question4":"What action are you inspired to make in light of what you have read?"},{"story":34,"bookId":42,"bookName":"Luke","ref":"16:1-17:10","prevPath":"/333","path":"/334","nextPath":"/335","display":"block","title":"More Teaching on True Discipleship","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?"},{"story":35,"bookId":42,"bookName":"Luke","ref":"17:11-19:27","prevPath":"/334","path":"/335","nextPath":"/336","display":"block","title":"More Power & Parables","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":36,"bookId":42,"bookName":"Luke","ref":"19:28-21:38","prevPath":"/335","path":"/336","nextPath":"/337","display":"block","title":"Jesus Entered Jerusalem","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":37,"bookId":42,"bookName":"Luke","ref":"22:1-22:71","prevPath":"/336","path":"/337","nextPath":"/338","display":"block","title":"Betrayed, Arrested & Denied","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?"},{"story":38,"bookId":42,"bookName":"Luke","ref":"23:1-24:53","prevPath":"/337","path":"/338","nextPath":"/339","display":"block","title":"Tried, Crucified & Risen","questionsTitle":"OPENING THE WORD","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?"},{"story":39,"bookId":43,"bookName":"John","prevPath":"/338","path":"/339","nextPath":"/340","display":"none","title":"Introduction to John","html":"<h6>That you may believe</h6><p>Jesus revealed as the Son of God, full of grace and truth</p><h6>How to read John</h6><p>In Jesus day, as in ours, there were many competing belief systemsall trying to connect with a spiritual reality beyond ourselves. One major religion features a god of power and revenge; another worships one that is silent and indifferent to the suffering of people; still another offers a god that is mysterious and unknowable, absorbing all of humanity into a great cosmic ocean of oneness. Some people worship gods of possessions, fame, and entertainment. Only one faithChristianityworships an amazing, personal being known primarily for his sacrificial love. This book profiles that unique God, revealed in the person of Jesus ChristGod in human flesh.</p><p>In this account of Jesus life, you will be confronted with some astonishing claims about who he is and what he came to do. John tells us that he has selected only a few of the many noteworthy things Jesus did in order to help us understand who Jesus is. He records only seven miracles, climaxing in Jesus resurrection. For John these signs give indisputable proof that Jesus is the Son of God. There is more, John says, but the world itself wouldnt have room for the books that would be written (Joh 21:25) had he recorded all of Jesus mighty signs. So why has he told us these select stories? To elicit a response of faith in our hearts, that you may believe that Jesus is the Christ, the Son of God, and that believing you may have life in his name (Joh 20:31).</p><p>Note some unique features of Johns gospel. He uses the images of light and life to describe Gods activity in the world. He includes several of Jesus sermons not found in the other gospels (3:10-21; 5:19-47; 9:41-10:5; 10:7-18; 10:25-30; 13:7; 13:8; 13:10; 13:12-20; 13:21; 13:26; 13:27; 13:31-35; 13:36; 13:38-14:4; 14:6-7; 14:9-21; 14:23-16; 16:19-28; 16:31-33; 17:1-26). He highlights Jesus many I am statements. Jesus declares he is the Messiah, the bread of life from heaven, the one sent by God, the light of the world, the door, the good shepherd, the Son of God, the resurrection, the life, the way, the truth, the true vine, and the King of the Jews. These statements should give you ample reason to believe!</p><h6>Who wrote this book?</h6><p>John, the apostleone of the original twelve disciples of Jesus.</p><h6>When and where was it written?</h6><p>Sometime between AD 80 and 95 (although some scholars argue that the book can be dated as early as the 50s and no later than 70). John was probably in Ephesus, a city located in modern-day Turkey.</p><h6>To whom was it written?</h6><p>Non-Jewish followers of Jesus, particularly those struggling with Greek philosophies, which taught that salvation comes through special knowledge and that Jesus was divine but not truly human. John insists that salvation is received by believing in Gods Son, Jesus, who came in human flesh.</p><h6>Why was it written?</h6><p>John himself clearly states his goal (see Joh 20:31). His writes the gospel with an evangelistic objective. He wants people to have eternal life by knowing Jesus Christ.</p><h6>SourceView Insights</h6><p>In John, three of the twelve disciples have no speaking part: Matthew-Levi, James son of Alphaeus, and Simon the Zealot (see the introduction to Matthew). Nine do in Johnmuch more than the other three gospel accounts. The disciple with the smallest speaking part is Jameshis own brother! Isnt that typical of a sibling?</p><p>Note the number of words attributed to each disciple within each gospel:<br /><br /><table style='font-size: 15px; max-width: 500px; margin: 0 auto'> <tr> <th></th> <th style='width: 12%'>Mat</th> <th style='width: 12%'>Mar</th> <th style='width: 12%'>Luk</th> <th style='width: 12%'>Joh</th> </tr><tr> <th>Simon Peter</th> <th>157</th> <th>120</th> <th>155</th> <th>130</th> </tr><tr> <th>Andrew</th> <th>-</th> <th>20</th> <th>17</th> <th>30</th> </tr><tr> <th>John the Disciple</th> <th>3</th> <th>100</th> <th>65</th> <th>8</th> </tr><tr> <th>James the Disciple</th> <th>3</th> <th>77</th> <th>38</th> <th>7</th> </tr><tr> <th>Philip the Disciple</th> <th>-</th> <th>-</th> <th>-</th> <th>55</th> </tr><tr> <th>Nathaniel-Bartholomew</th> <th>-</th> <th>-</th> <th>-</th> <th>25</th> </tr><tr> <th>Thomas</th> <th>-</th> <th>-</th> <th>-</th> <th>50</th> </tr><tr> <th>Judas-Thaddaeus</th> <th>-</th> <th>-</th> <th>-</th> <th>18</th> </tr><tr> <th>Judas Iscariot</th> <th>40</th> <th>16</th> <th>-</th> <th>14</th> </tr></table><br />In all four gospel narratives, Simon Peter has the lead speaking role among the disciples. After him Judas Iscariot follows in Matthew; John the Disciple follows in both Mark and Luke; but Thomas has that honor in John. In light of Johns stated purpose (Joh 20:31), Thomas struggle to believe that Jesus rose from the dead was a key way for John to underscore the importance of faith and the trustworthiness of the testimony of those who were the first eyewitnesses of Jesus resurrection. We are often too harsh on Thomas, nicknaming him doubting (20:25) when in fact he also modeled radical commitment. He was the first to express his willingness to lay down his life for the Lord. His statement, Lets go also, that we may die with him. (11:16) may not have been overly optimistic and hope-filled, but it certainly expressed a willing, committed heart! To what lengths will you go in your walk with Jesus?</p>"},{"story":40,"bookId":43,"bookName":"John","ref":"1:1-1:51","prevPath":"/339","path":"/340","nextPath":"/341","display":"block","title":"God Came to Us","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?"},{"story":41,"bookId":43,"bookName":"John","ref":"2:1-4:42","prevPath":"/340","path":"/341","nextPath":"/342","display":"block","title":"The First Sign","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you see any comparisons made by the author of this passage? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you see, describe any contrasts made by the author of this passage? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have noted in this passage.","Question4":"Please take time to tell a friend what God is teaching you through this reflection on the Bible."},{"story":42,"bookId":43,"bookName":"John","ref":"4:43-5:47","prevPath":"/341","path":"/342","nextPath":"/343","display":"block","title":"Signs Two & Three","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this portion of Scripture? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this portion of Scripture?","Question3":"What do you think God was wanting accomplish through the life of your selected character?","Question4":"What do you think God is wanting to accomplish through your life? How can you cooperate with his kind purposes in your life?"},{"story":43,"bookId":43,"bookName":"John","ref":"6:1-6:71","prevPath":"/342","path":"/343","nextPath":"/344","display":"block","title":"Signs Four & Five","questionsTitle":"DIGGING DEEPER","Question1":"What were the two signs Jesus performed in this story?","Question2":"Jesus' acts and his words are linked together. How do his words help you understand the signs better? ","Question3":"What most amazes you about Jesus in this story?","Question4":"How do people respond to Jesus' signs and teaching? How will you respond to Jesus' signs and teaching?"},{"story":44,"bookId":43,"bookName":"John","ref":"7:1-8:59","prevPath":"/343","path":"/344","nextPath":"/345","display":"block","title":"Who is This Doer of Signs?","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":45,"bookId":43,"bookName":"John","ref":"9:1-10:42","prevPath":"/344","path":"/345","nextPath":"/346","display":"block","title":"Sign Six","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":46,"bookId":43,"bookName":"John","ref":"11:1-12:50","prevPath":"/345","path":"/346","nextPath":"/347","display":"block","title":"Sign Seven","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this portion of Scripture? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this portion of Scripture?","Question3":"What do you think God was wanting accomplish through the life of your selected character?","Question4":"What do you think God is wanting to accomplish through your life? How can you cooperate with his kind purposes in your life?"},{"story":47,"bookId":43,"bookName":"John","ref":"13:1-14:31","prevPath":"/346","path":"/347","nextPath":"/348","display":"block","title":"Last Supper: Servanthood & Identity","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?"},{"story":48,"bookId":43,"bookName":"John","ref":"15:1-17:26","prevPath":"/347","path":"/348","nextPath":"/349","display":"block","title":"Last Supper: Fruitfulness & Unity","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?"},{"story":49,"bookId":43,"bookName":"John","ref":"18:1-19:42","prevPath":"/348","path":"/349","nextPath":"/350","display":"block","title":"Tried, Crucified & Buried","questionsTitle":"DIGGING DEEPER","Question1":"What can you learn from Jesus about how he faced suffering and injustice?","Question2":"What did Peter say when he was asked if he was one of Jesus' disciples? Why do you think he answered this way?","Question3":"Imagine how Peter felt in this story? When have you felt similar emotions? How did you respond?","Question4":"Spend some time talking to God about your personal experiences regarding suffering, injustice and denial. Allow God to lead you to an appropriate response."},{"story":50,"bookId":43,"bookName":"John","ref":"20:1-21:25","prevPath":"/349","path":"/350","nextPath":"/351","display":"block","title":"The Ultimate Sign: Christ's LIfe-giving Resurrection!","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":51,"bookId":44,"bookName":"Acts","prevPath":"/350","path":"/351","nextPath":"/352","display":"none","title":"Introduction to Acts","html":"<h6>Good news to all people</h6><p>An explosion thats still shaking the world</p><h6>How to read Acts</h6><p>After his death Jesus ministry continued in the life of the early church and continues today through the lives of his people. Acts is the story of those early believers, who sought to apply what it meant to live out the missional teaching of Jesus in their day through the power of the Holy Spirit. Their story is our encouragement: despite difficulties, the church can survive and even thrive. Transformation, revival, and church growth come, not through human effort alone, but in partnership with the Holy Spirit.</p><p>Thirty years of history are covered, yet every page abounds with precise details and dramatic descriptions. The opening chapters tell how the church began to be multiplied, through the presence and the power of the Spirit. Dynamic growth happened as the gospel broke down age-old cultural walls between Jews and Gentiles. The church then expanded its witness geographically far beyond Jerusalem. Peter and Paul are the key characters in these stories: Peter as an anchor for Jewish believers in Jerusalem, and Paul striking out into new lands as the churchs first great missionary to the Gentiles.</p><p>We have inherited the promise and the mandate Jesus first entrusted to these believers. The same Holy Spirit so active in Acts is still at work in our story today, so let us draw on the zeal and faith of our earliest brothers and sisters. It is now our turn to be his witnesses to the uttermost parts of the earth (Act 1:8).</p><h6>Who wrote this book and when?</h6><p>Luke, the author of the gospel that bears his name, wrote Acts in approximately AD 6370. The books of Luke and Acts compose almost one-fourth of the New Testament.</p><h6>To whom was it written and why?</h6><p>As an historian, Luke wrote to Theophilus (a name that means lover of God) to tell what happened after Jesus resurrection. Acts is the second volume of the Good Newsthe sequel to the gospel. In it Luke explained Christianitys amazing growth, perhaps to legitimize the church to civil authorities or to confirm the faith of believers. Luke may also have wanted believers to understand how the Spirit was working to do away with the historic conflict between Jews and Gentiles.</p><h6>SourceView Insights</h6><p>There are two lead actors in Acts: Peter and Paul. Peter speaks a total of 36 times 31 times alone and 5 times together with others. Paul speaks a total of 56 times 53 times alone and 3 times together with others. The words of these two fill more than one-fourth of the text of Acts (Peter 10 percent and Paul 16 percent). Consider these parallels:<br /><br /><table style='max-width:800px; margin: 0 auto; font-size: 18px'> <tr> <th colspan='3' style='text-align: center'>Parallels Between Peter and Paul in Acts</th></tr><tr> <th style='width: 50%; font-weight: bold'>Both...</th> <th style='width: 25%; font-weight: bold'>Peter</th> <th style='width: 25%; font-weight: bold'>Paul</th> </tr><tr> <th>heal a cripple from birth</th> <th>3:6</th> <th>14:10</th> </tr><tr> <th>were plotted against</th> <th>4:1-3; 5:17-18; 12:2-5</th> <th>9:23-25,29; 13:45-50; 14:19; 16:19-39; 17:5-9,13; 19:23-41; 20:3; 21:27-35; 23:12-15; 25:2-3</th> </tr><tr> <th>testify before the Jewish high council</th> <th>4:5-22; 5:17-41</th> <th>22:30-23:10</th> </tr><tr> <th>do extraordinary miracles</th> <th>5:15-16</th> <th>19:11-12</th> </tr><tr> <th>are supernaturally released from prison</th> <th>5:18-25; 12:6-16</th> <th>16:23-40</th> </tr><tr> <th>preach boldly amidst persecution</th> <th>5:21</th> <th>14:20</th> </tr><tr> <th>pray for Holy Spirit baptism</th> <th>8:17</th> <th>19:6</th> </tr><tr> <th>encounter a sorcerer</th> <th>8:18-24</th> <th>13:8-11</th> </tr><tr> <th>raise the dead</th> <th>9:36-42</th> <th>20:7-12</th> </tr><tr> <th>have a 'Gentile' vision</th> <th>10:10-16</th> <th>16:9-10</th> </tr><tr> <th>are worshiped by men</th> <th>10:25</th> <th>14:11-18</th> </tr><tr> <th>preach to Roman official's household</th> <th>10:24</th> <th>16:31</th> </tr><tr> <th>'Conversion' told 1st time</th> <th>10:1-47</th> <th>9:1-22</th> </tr><tr> <th>'Conversion' told 2nd time</th> <th>11:4-18</th> <th>22:3-21</th> </tr><tr> <th>'Conversion' told 3rd time</th> <th>15:7-11</th> <th>26:2-23</th> </tr></table><br />Read and reflect on the three longest speeches of each of these two lead actors, noting the similarities and differences between them.<br /><br />Peters main speeches are:</p><ul>  <li>His message in the temple of Jerusalem on the day of Pentecost (1:20-22; 2:14-36; 2:38-39);</li><br /><li>His message in the temple of Jerusalem after healing a man lame from birth (3:12-26);</li><br /><li>His testimony to the believers in Jerusalem after meeting with Cornelius (11:5-15; 11:15-17).</li></ul><p><br />Pauls main speeches are:</p><ul>  <li>His message in the synagogue of Antioch of Pisidia on the first missionary journey (13:16, 13:16-41);</li><br /><li>His testimony in the temple of Jerusalem at the time of his arrest (22:1; 22:3-21);</li><br /><li>His testimony in Caesarea before Herod Agrippa and Governor Porcius Festus (26:2-23; 26:25-27; 26:29).</li></ul><p><br />In addition to Peter and Paul, there are an additional 61 speaking parts in Acts including individuals and groups, human, demonic, and angelic. These blue sections of Acts occupy 30 percent of the book. The three longest blue speeches are:</p><ul>  <li>Stephens message in Jerusalem when on trial before the Jewish high council (7:2-53);</li><br /><li>Festus explanation to Herod Agrippa about Pauls case (25:14-21);</li><br /><li>Jamess decision at the conclusion of the council of Jerusalem (15:13-21).</li></ul>"},{"story":52,"bookId":44,"bookName":"Acts","ref":"1:1-2:47","prevPath":"/351","path":"/352","nextPath":"/353","display":"block","title":"Ascension & Pentecost","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you discover any comparisons made by the author of this story? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you discover any contrasts made by the author of this story? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have found in this story.","Question4":"Please take time to write a short prayer to God responding to what you have discovered through these comparisons, contrasts and repetitions."},{"story":53,"bookId":44,"bookName":"Acts","ref":"3:1-5:11","prevPath":"/352","path":"/353","nextPath":"/354","display":"block","title":"Growth & Persecution","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this passage? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this passage?","Question3":"What do you think God was wanting do in the life of your selected character?","Question4":"What do you think God is wanting to do in your life as you reflect on this passage? How can you cooperate with his loving purposes this week?"},{"story":54,"bookId":44,"bookName":"Acts","ref":"5:12-8:4","prevPath":"/353","path":"/354","nextPath":"/355","display":"block","title":"Multiplication & Martyrdom","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you describe any comparisons made by the author of this portion of Scripture? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you describe any contrasts made by the author of this story portion of Scripture? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have observed in this portion of Scripture.","Question4":"Please take time to post on your social media what you have learned through this passage."},{"story":55,"bookId":44,"bookName":"Acts","ref":"8:5-9:31","prevPath":"/354","path":"/355","nextPath":"/356","display":"block","title":"Paul's Conversion","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":56,"bookId":44,"bookName":"Acts","ref":"9:32-12:25","prevPath":"/355","path":"/356","nextPath":"/357","display":"block","title":"Cornelius' Conversion","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given guidance can you observe in this passage?","Question2":"Put yourself in the passage and envision what it might have been like to receive this guidance. How does this relate to you and your family today?","Question3":"What God-inspired encouragement can you observe in this passage? How can you experience more of this in your own life?","Question4":"Write a poem about what this encouragement means for you."},{"story":57,"bookId":44,"bookName":"Acts","ref":"13:1-14:28","prevPath":"/356","path":"/357","nextPath":"/358","display":"block","title":"First Missionary Journey","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"Does this portion of Scripture address a character quality you should seek to cultivate? What is it?","Question2":"What actions can you embark on in your life to do this?","Question3":"Does this portion of Scripture address a character flaw you should seek to uproot? What is it?","Question4":"What actions can you embark on your life to do this?"},{"story":58,"bookId":44,"bookName":"Acts","ref":"15:1-15:36","prevPath":"/357","path":"/358","nextPath":"/359","display":"block","title":"Council at Jerusalem","questionsTitle":"DIGGING DEEPER","Question1":"What issue did the apostles and elders gather in Jerusalem to consider?","Question2":"According to Peter in this story how are believers saved?","Question3":"Do you feel the need to fulfill certain religious expectations to be saved?","Question4":"Just as the Holy Spirit confirmed the truth of the gospel to apostles and elders, what is the Holy Spirit saying to you?"},{"story":59,"bookId":44,"bookName":"Acts","ref":"15:37-18:22","prevPath":"/358","path":"/359","nextPath":"/360","display":"block","title":"Second Missionary Journey","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?"},{"story":60,"bookId":44,"bookName":"Acts","ref":"18:23-21:16","prevPath":"/359","path":"/360","nextPath":"/361","display":"block","title":"Third Missionary Journey","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":61,"bookId":44,"bookName":"Acts","ref":"21:17-23:11","prevPath":"/360","path":"/361","nextPath":"/362","display":"block","title":"Paul Arrested in Jerusalem","questionsTitle":"OPENING THE WORD","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?"},{"story":62,"bookId":44,"bookName":"Acts","ref":"23:12-26:32","prevPath":"/361","path":"/362","nextPath":"/363","display":"block","title":"Paul on Trial in Caesarea","questionsTitle":"DIGGING DEEPER","Question1":"Paul gives his testimony twice in this passage. How does he describe his life before meeting Jesus?","Question2":"How does Paul describe his encounter with Jesus?","Question3":"How does Paul describe his life as a follower of Jesus?","Question4":"Write your own story, including these three elements: your life before Jesus, your encounter with Jesus, and your life as a follower of Jesus. Make a plan to share your story with someone this week."},{"story":63,"bookId":44,"bookName":"Acts","ref":"27:1-28:31","prevPath":"/362","path":"/363","nextPath":"/364","display":"block","title":"Paul Sent to Rome","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this story ? Why?","Question2":"What strengths and/or weaknesses do you see in this character?","Question3":"What do you think God was wanting your selected character to learn?","Question4":"What do you think God is wanting you to learn through this story? How can you cooperate with his good purposes today?"},{"story":64,"bookId":45,"bookName":"Romans","prevPath":"/363","path":"/364","nextPath":"/365","display":"none","title":"Introduction to Romans","html":"<h6>The power of God for salvation</h6><p>The gospelthe divine solution for humanitys common need</p><h6>How to read Romans</h6><p>The ultimate power for a transformed life is the power of God. Romans reveals that God has won the victory over sin and death through Jesus, who paid the penalty by dying in our place. He broke sins enslaving power over us. Through Gods power, Christians can reflect the attitudes and actions of those who are deeply loved by God. A prayerful study of Romans will uncover the key to the Spirit-filled life: a simple and ongoing response of faith in Jesus and his work on the cross. Be prepared! Martin Luther, John Wesley, and many other notable persons of faith found their lives transformed by the message of this book. They then went on to become world-changers as they applied the truth that faith is all that is necessary to become acceptable to God. Like these men and women of God, take hold of this book until its message takes hold of you!</p><p>Romans is one of the most highly organized books in the New Testament. After a brief introduction, Paul declares that all humans, regardless of background or nationality, are sinners and thus are not able to have a relationship with God (Rom 1:18-3:20). Next he explains how God justly dealt with sin, making the divine-human friendship possible (Rom 3:21-8:39). Paul then argues that our faith in response to Gods work through the cross is the key for our justification (salvation) and continues to be the way to gain access to the power of the indwelling Christ in order to say no to sin and yes to God. In chapters 9-11 Paul summarizes how Gods redemptive work through history has prepared a way for the Jews as well as for people of every other nation (Gentiles) to benefit from his gift of grace through Jesus death on the cross. In the final five chapters youll receive practical guidance on how to live out your faith in unity with other believers so that that with one accord you may with one mouth glorify the God and Father of our Lord Jesus Christ (Rom 15:6).</p><h6>Who wrote this book and when?</h6><p>The apostle Paul, who wrote about the grace of God both from experience (Act 9:1-19) and education (Act 22:3), wrote these words about AD 57.</p><h6>To whom was it written and why?</h6><p>Paul wrote to predominantly Gentile believers in the capital city of the Roman Empire:</p><ol>  <li>to introduce himself to the believers there and enlist their help in spreading the gospel;</li><br /><li>to develop and defend the truth of the gospel he had been preaching; and</li><br /><li>to encourage the Roman believers to rely solely on Gods grace for their salvation (Rom 3:24).</li></ol><h6>SourceView Insights</h6><p>Romans is the first of twenty books in the New Testament that are actually letters written to either an individual or a group. Paul wrote thirteen of these letterseach of his letters bears the name of the intended recipient. The final seven letters were authored by various church leadersand each is named after its respective author.</p><p>Reading these letters is like listening to one side of a telephone conversation. You actually do not hear the dialogue, but you are aware that what you are hearing is part of a larger, ongoing conversation between several parties. Because these letters contain only one side of that dialogue, all the SourceView text is in black. Though the letters are embedded in a dramatic life narrative, they themselves do not contain dramatic textual interchange.</p><p>In order to help us relive the experience that it must have been for the original recipients to have read these divinely-inspired letters, we have displayed the letters with a cursive script to recreate the original handwritten feel of these very personal documents. None of the letters takes long to read, so pick one up and read it from beginning to end at one sittingjust like you would if you received a letter from a dear friend. Reading these letters does not take nearly as long as you may think. They average less than 20 minutes a book! Here are some time frames to give you an idea of how long it might take to read each of the New Testament letters:</p><br /><table style='max-width:300px; margin: 0 auto; font-size: 18px'><tr><th>Letter</th><th>Read Time</th></tr><tr><th>Romans</th><th>1 hour</th></tr><tr><th>1 Corinthians</th><th>1 hour</th></tr><tr><th>2 Corinthians</th><th>1 hour</th></tr><tr><th>Galatians</th><th>20 Minutes</th></tr><tr><th>Ephesians</th><th>20 minutes</th></tr><tr><th>Philippians</th><th>15 minutes</th></tr><tr><th>Colossians</th><th>15 minutes</th></tr><tr><th>1 Thessalonians</th><th>15 minutes</th></tr><tr><th>2 Thessalonians</th><th>10 minutes</th></tr><tr><th>1 Timothy</th><th>15 minutes</th></tr><tr><th>2 Timothy</th><th>10 minutes</th></tr><tr><th>Titus</th><th>10 minutes</th></tr><tr><th>Philemon</th><th>5 minutes</th></tr><tr><th>Hebrews</th><th>45 minutes</th></tr><tr><th>James</th><th>15 minutes</th></tr><tr><th>1 Peter</th><th>15 minutes</th></tr><tr><th>2 Peter</th><th>10 minutes</th></tr><tr><th>1 John</th><th>15 minutes</th></tr><tr><th>2 John</th><th>5 minutes</th></tr><tr><th>3 John</th><th>5 minutes</th></tr><tr><th>Jude</th><th>5 minutes</th></tr></table>"},{"story":65,"bookId":45,"bookName":"Romans","ref":"1:1-3:20","prevPath":"/364","path":"/365","nextPath":"/366","display":"block","title":"Jews & Gentiles Have Sinned","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you see any comparisons made by the author of this passage? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you see, describe any contrasts made by the author of this passage? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have noted in this passage.","Question4":"Please take time to tell a friend what God is teaching you through this reflection on the Bible."},{"story":66,"bookId":45,"bookName":"Romans","ref":"3:21-5:21","prevPath":"/365","path":"/366","nextPath":"/367","display":"block","title":"Jews & Gentiles Justified by Faith","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"Does this portion of Scripture address a character quality you should seek to cultivate? What is it?","Question2":"What actions can you embark on in your life to do this?","Question3":"Does this portion of Scripture address a character flaw you should seek to uproot? What is it?","Question4":"What actions can you embark on your life to do this?"},{"story":67,"bookId":45,"bookName":"Romans","ref":"6:1-8:39","prevPath":"/366","path":"/367","nextPath":"/368","display":"block","title":"God's Children Adopted by the Spirit","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":68,"bookId":45,"bookName":"Romans","ref":"9:1-11:36","prevPath":"/367","path":"/368","nextPath":"/369","display":"block","title":"History of Redemptive Mercy","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?"},{"story":69,"bookId":45,"bookName":"Romans","ref":"12:1-15:13","prevPath":"/368","path":"/369","nextPath":"/370","display":"block","title":"Fruit of Transformational Grace","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":70,"bookId":45,"bookName":"Romans","ref":"15:14-16:27","prevPath":"/369","path":"/370","nextPath":"/371","display":"block","title":"Travel Plans and Greetings","questionsTitle":"DIGGING DEEPER","Question1":"What is Paul's stated ambition? How is it like your ambitions? How is it different? ","Question2":"Consider all the individuals Paul greets. What does this tell about how much he valued people and relationships?","Question3":"What inspired Paul to make travel plans?","Question4":"When you consider the future how much do kingdom goals shape your plans? Are there changes this passage would inspire you to make?"},{"story":71,"bookId":46,"bookName":"1 Corinthians","prevPath":"/370","path":"/371","nextPath":"/372","display":"none","title":"Introduction to 1 Corinthians","html":"<h6>Make love your aim</h6><p>Words of wisdom for a church in turmoil</p><h6>How to read 1 Corinthians</h6><p>Fights. Rumors. Factions. Its all here in 1 Corinthians. Few other passages of scripture reveal the weaknesses of Christians as vividly as this book does. Some other topics include: Dealing with a sex-crazed society. Divorcewhen is it justified? When Christians can and cannot sue. Get ready! Youre about to encounter Gods perspective on some hot topics. And in the process you will see how to encourage believers to make a godly impact on todays world.</p><p>Notice how Paul sees believers as ones who are holy and calledin spite of their sometimes unholy behavior. Watch how he skillfully strives to wake them up to the fact that they are the dwelling place of the Holy Spirit (1Co 3:16; 6:19). Look for the obvious emotions that filled Pauls heart as he wrote this letter. You will observe a wide range of moods expressed here, from anger to shame to sorrow to tenderness. He longs for believers to adjust their behavior to reflect the righteousness of Jesus. Pauls words correct jealousies and pride, steering people away from extremes that undermine Christian unity and love. This book deserves repeated readings. Your time will be well spent as you examine areas in your own life where you may need to make a change.</p><h6>Who wrote this book and when?</h6><p>The apostle Paul wrote it about AD 54 or 55.</p><h6>To whom was it written?</h6><p>Christians in Corinth, an important commercial city in Greece.</p><h6>Why was it written?</h6><p>Two or three years after leaving the church hed started in Corinth, Paul heard disturbing reports: Strife and division were seriously threatening the young church. Some had become spiritually arrogant, leading to problems such as sexual misconduct, wrongdoing against other believers, abuse of spiritual gifts and misunderstandings of basic Christian teachings. Paul wrote to restore the church.</p><h6>SourceView Insights</h6><p>Pauls letter to the Christians in Corinth contains only one side of his dialogue with them; therefore all the SourceView text is in black. This letter is displayed in a cursive script to give the original handwritten feel of such a personal document.</p>"},{"story":72,"bookId":46,"bookName":"1 Corinthians","ref":"1:1-4:21","prevPath":"/371","path":"/372","nextPath":"/373","display":"block","title":"Concerning Unity","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":73,"bookId":46,"bookName":"1 Corinthians","ref":"5:1-7:40","prevPath":"/372","path":"/373","nextPath":"/374","display":"block","title":"Concerning Sex & Marriage","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you see any comparisons made by the author of this passage? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you see, describe any contrasts made by the author of this passage? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have noted in this passage.","Question4":"Please take time to tell a friend what God is teaching you through this reflection on the Bible."},{"story":74,"bookId":46,"bookName":"1 Corinthians","ref":"8:1-11:1","prevPath":"/373","path":"/374","nextPath":"/375","display":"block","title":"Concerning Freedom & Self-control","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this portion of Scripture? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this portion of Scripture?","Question3":"What do you think God was wanting accomplish through the life of your selected character?","Question4":"What do you think God is wanting to accomplish through your life? How can you cooperate with his kind purposes in your life?"},{"story":75,"bookId":46,"bookName":"1 Corinthians","ref":"11:2-14:40","prevPath":"/374","path":"/375","nextPath":"/376","display":"block","title":"Concerning Love & Worship","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given commands can you discover in this story?","Question2":"Put yourself in the story and imagine what it originally might have been like to receive this command. How does this relate to you in your personal life today?","Question3":"What God-inspired promises can you discover in this story? How can you walk in more of this in your own life?","Question4":"Draw a picture about what these promises mean for you."},{"story":76,"bookId":46,"bookName":"1 Corinthians","ref":"15:1-16:24","prevPath":"/375","path":"/376","nextPath":"/377","display":"block","title":"Concerning the Resurrection & Generosity","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":77,"bookId":47,"bookName":"2 Corinthians","prevPath":"/376","path":"/377","nextPath":"/378","display":"none","title":"Introduction to 2 Corinthians","html":"<h6>Pleas of a concerned father</h6><p>Paul bares his heart in a desperate attempt to win back a wandering church</p><h6>How to read 2 Corinthians</h6><p>Cell phone conversations go on around us all the time; you can tell how someone feels about what theyre talking about, even though you can only hear one side of the conversation! Reading 2 Corinthians is something like that. We may not know all the details, but the feelings come through loud and clear as Paul lays out his joys, sorrows, ambitions, and frustrations for the believers at Corinth. His vulnerability gives us deep insight for our own relationships with God.</p><p>The letter divides nicely into three sections. In the first seven chapters, Paul describes both the glory of the gospel message and his experiences as a minister of Jesus Christ. In the next two chapters, Paul undertakes a fund-raising campaign for the poverty-stricken church in Jerusalem. In the last four chapters, he defends himself against church members who refused to recognize his authority as an apostle and leader. In the midst of it all, a powerful lesson shines through: In your weakness, you discover Gods power!</p><p>Be especially alert for practical examples and advice on resolving conflict: personality conflicts between church members, theological conflicts over false teachings, and cultural conflicts between the church and the world.</p><h6>Who wrote this book and why?</h6><p>The apostle Paul wrote it approximately AD 55. Inner strife had plagued the church at Corinth. Paul wrote to calm the disagreements, to restore unity to the body of believers, and to reestablish his role as leader.</p><h6>How does it fit in the big story?</h6><p>Even this early on, churches like Corinth had problems running smoothly. Pauls instructions on handling dissension within the church, false teaching, and church leadership were given to help resolve these recurring problems. He also draws believers attention outward by seeking their help for the poorer believers in Jerusalem.</p><h6>SourceView Insights</h6><p>All the SourceView text is black because this letter contains only one side of Pauls dialogue with the church at Corinth. The letter is displayed in a cursive script, recreating the original handwritten feel of this personal document.</p>"},{"story":78,"bookId":47,"bookName":"2 Corinthians","ref":"1:1-3:18","prevPath":"/377","path":"/378","nextPath":"/379","display":"block","title":"Ministers of the Gospel","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"Does this portion of Scripture address a character quality you should seek to cultivate? What is it?","Question2":"What actions can you embark on in your life to do this?","Question3":"Does this portion of Scripture address a character flaw you should seek to uproot? What is it?","Question4":"What actions can you embark on your life to do this?"},{"story":79,"bookId":47,"bookName":"2 Corinthians","ref":"4:1-7:16","prevPath":"/378","path":"/379","nextPath":"/380","display":"block","title":"Ministers by Grace","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?"},{"story":80,"bookId":47,"bookName":"2 Corinthians","ref":"8:1-9:15","prevPath":"/379","path":"/380","nextPath":"/381","display":"block","title":"Practice Generosity","questionsTitle":"DIGGING DEEPER","Question1":"What is the example of the Macedonian church that Paul shares with the Corinthians?","Question2":"What is Paul's expectation of the Corinthians?","Question3":"What can you learn about generosity from Paul's teaching?","Question4":"In light of what you've learned, ask God to guide you to give generously this week to someone in need."},{"story":81,"bookId":47,"bookName":"2 Corinthians","ref":"10:1-13:14","prevPath":"/380","path":"/381","nextPath":"/382","display":"block","title":"True and False Ministers","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the many questions raised in this passage. Who makes them? To whom are they made?","Question2":"Is there a question that impacts you more than others? Why?","Question3":"What questions would do you have about issues raised in this passage?","Question4":"What can you do to get some answers to these questions you have raised?"},{"story":82,"bookId":48,"bookName":"Galatians","prevPath":"/381","path":"/382","nextPath":"/383","display":"none","title":"Introduction to Galatians","html":"<h6>No additives needed</h6><p>A call to hold fast to the liberating truth of the gospel</p><h6>How to read Galatians</h6><p>People who care about nutrition often read the labels before buying packaged foods. Why? To check for additives and ingredients that may be harmful. In a similar way, Galatians describes the toxic effect of mixing legalism and human works into the simple gospel. This book offers a spiritual health checka clear explanation of what it means to be saved by faith.</p><p>The usual format of a common first-century letter follows a different pattern from what we are accustomed to today. The expected literary flow would begin by stating who the letter was from and to whom it was directed (like an email header). Then this would be followed by a brief greeting and a prayer of blessing or thanksgiving before proceeding to the main body of the correspondence. But this was no normal letter. After the expected cross-cultural greeting (grace for Gentiles and peace for Jewsthe normal terms used by Greek-speakers and Hebrew-speakers; Gal 1:3), Paul forgoes the prayer of blessing. Instead he curses his readers! Twice! (see Gal 1:6-10). Wow! Now he had their attention! Does he have yours? He didnt care about literary conventions. He wanted to rescue his beloved Galatians from the danger of following a false gospel.</p><p>Paul proceeds with a passionate and dramatic retelling of his own encounter with Christ. He vehemently shares ministry highlights that establish his credentials as an apostle. His own experience of how Jesus changed his life is what has equipped him to be a defender of the true gospel of salvation by grace through faith. Next he unpacks a number of Old Testament characters to illustrate that the gospel he proclaims is in keeping with Gods purposes ever since the days of Abraham! If this is true then there is no more room for bondage to old legalistic religious systems. He practically shouts, Stand firm therefore in the liberty by which Christ has made us free, and dont be entangled again with a yoke of bondage (Gal 5:1). He then explains the practical implications of the gospel upon a life set free by grace and lived out under the control of the Holy Spirit. This life is characterized by love, joy, peace, patience, kindness, goodness, faith, gentleness, and self-control (Gal 5:22-23).</p><p>Galatians will take you back to the basicswhat the gospel is, how you receive it, and how you can apply it in your daily life. At times, Paul gets downright feisty, even resorting to sarcasm, to grab the Galatians attention and win them back to a gospel of liberty. May his passion jolt you back into the shocking news of just how free we are in Christ!</p><h6>Who wrote this book and when?</h6><p>Paul, the apostle, wrote it probably after completing the first missionary journey (Act 13-14) and just before the council of Jerusalem (Act 15). The disagreement mentioned by Paul in Act 15:2 could well be the story that Paul describes in Gal 2:11-16. It seems that after his run-in with Peter in Antioch, Paul wrote this passionate epistle, as he was preparing for the debate that would ensue in Jerusalem. This would have taken place about 25 years after Jesus death and resurrection.</p><h6>To whom was it written and why?</h6><p>To Christians in Galatia, a Roman province in the central part of what is now called Turkey, to denounce and correct false teachings that had infiltrated the churches Paul and Barnabas had earlier established. False teachers insisted that Gentile Christians had to keep the ritual laws of the Jews. Paul, stinging from personal attacks against him, also wrote to defend his integrity as an apostle and to reassert his love for the Galatians.</p><h6>SourceView Insights</h6><p>This letter contains only one side of Pauls dialogue with the Galatians, so all the SourceView text is in black. The letter is displayed in a cursive script to recreate the original handwritten feel of this personal document.</p>"},{"story":83,"bookId":48,"bookName":"Galatians","ref":"1:1-2:21","prevPath":"/382","path":"/383","nextPath":"/384","display":"block","title":"How God Revealed the Gospel","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this portion of Scripture? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this portion of Scripture?","Question3":"What do you think God was wanting accomplish through the life of your selected character?","Question4":"What do you think God is wanting to accomplish through your life? How can you cooperate with his kind purposes in your life?"},{"story":84,"bookId":48,"bookName":"Galatians","ref":"3:1-6:18","prevPath":"/383","path":"/384","nextPath":"/385","display":"block","title":"Foundation and Fruit of the Gospel","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given commands can you discover in this story?","Question2":"Put yourself in the story and imagine what it originally might have been like to receive this command. How does this relate to you in your personal life today?","Question3":"What God-inspired promises can you discover in this story? How can you walk in more of this in your own life?","Question4":"Draw a picture about what these promises mean for you."},{"story":85,"bookId":49,"bookName":"Ephesians","prevPath":"/384","path":"/385","nextPath":"/386","display":"none","title":"Introduction to Ephesians","html":"<h6>Together in Christ!</h6><p>Living worthy of our rich inheritance in Jesus Christ</p><h6>How to read Ephesians</h6><p>Satisfying our thirst for identity and fulfillment is not found in the adventure of an exotic safari, the success of a booming business venture, or the passion of a romantic relationship with that perfect someone. Rather it occurs as we discover the purpose for our lives. This letter answers the question men and women have asked throughout all time, Why am I here? The answer may astound you. Interested? Read on. Our journey with God is only beginning, and it promises to get better and better!</p><p>Paul uses the biggest, most extravagant words to paint the picture of how incredible it is to discover our identity and purpose as Gods beloved. Dont rush through this superlative-laden description of the indescribable riches of Gods redemptive acts! Take time to savor each nuance of meaning as Paul reveals who God is and his measureless love for you (Eph 1:17-23; 3:15-21). Join Paul in worshiping God for the unsearchable riches of Christ (Eph 3:8) because of his incomparable sacrifice on the cross!</p><p>This letter divides into three parts: Chapters 1-3 speak of the Christians wealth in Christ; Eph 4:1-6:9 of the Christians walk; Eph 6:10-24 of the Christians warfare. As you study this book, youll learn about Gods intentions for his people, and you will gain insight into the nature of the church. Notice how Paul uses various word pictures to stress the unity of all believers.</p><h6>Who wrote this book and why?</h6><p>The apostle Paul wrote it sometime during his imprisonment in Rome, around AD 6062. He wrote to the churches of the Roman province of Asia (Western Turkey today) to remind them of their new identity in Christ. Ephesus was the capital city of that province.</p><h6>To whom was it written and why?</h6><p>Paul wrote to believers in Ephesus and the surrounding province. Ephesus was a world-famous center of idol worship in the Roman World. It was a magnet for sexual immorality and every pleasure-seeking lifestyle imaginable. At the same time, ungodly philosophies and spiritual occultism competed for attention in the citys prosperous market place. The new converts who came out of this environment needed a whole new way of seeing themselves and relating to God! Paul wanted them to think of themselves as people in Christpeople with a radically new identity (Eph 2:12-13).</p><h6>SourceView Insights</h6><p>Because this letter contains only one side of the dialogue between Paul and the Ephesians, all the SourceView text is in black. The letter is displayed in a cursive script to recreate the original handwritten feel of such a personal document.</p>"},{"story":86,"bookId":49,"bookName":"Ephesians","ref":"1:1-3:21","prevPath":"/385","path":"/386","nextPath":"/387","display":"block","title":"Adopted into God's Family","questionsTitle":"OPENING THE WORD","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?"},{"story":87,"bookId":49,"bookName":"Ephesians","ref":"4:1-6:24","prevPath":"/386","path":"/387","nextPath":"/388","display":"block","title":"Living as God's Family","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"Does this portion of Scripture address a character quality you should seek to cultivate? What is it?","Question2":"What actions can you embark on in your life to do this?","Question3":"Does this portion of Scripture address a character flaw you should seek to uproot? What is it?","Question4":"What actions can you embark on your life to do this?"},{"story":88,"bookId":50,"bookName":"Philippians","prevPath":"/387","path":"/388","nextPath":"/389","display":"none","title":"Introduction to Philippians","html":"<h6>An overcoming joy</h6><p>Pressing on! A lifetime pursuit of intimacy with God</p><h6>How to read Philippians</h6><p>Joy, joy, and more joy! Its not the impossible happily ever after of your favorite fairy tale; Paul describes the current reality every believer can experience! He confidently claims that nothing can stamp out the joy we have in Jesus! There is joy in living even in the midst of painful conflicts; there is joy in serving in a spirit of humility; there is joy in knowing Jesus and in making him and his incomparable blessings known; and there is joy when we walk in contentment and gratitude.</p><p>At the heart of this joy-filled, thank-you letter to his dear friends and supporters in Philippi, Paul shares the driving ambition of his life. Even after more than two decades of Christian service, Paul passionately declares, that I may know him (Phl 3:10). In his insatiable hunger for God, Paul yearned for an ever-growing intimacy with him, a desire he hoped would fill the heart of every believer.</p><p>Among many gems, youll find one of the Bibles most prominent psalms of praise to Jesus (Phl 2:5-11); youll see the futility of religious activity compared to a relationship with Jesus (Phl 3:4-11); and youll gain practical tools to help reshape your thinking according to Gods ways (Phl 4:4-9).</p><h6>Who wrote this book and when?</h6><p>The apostle Paul wrote it about AD 6062, while under house arrest in Rome where he was awaiting trial on an appeal to the Roman emperor Nero.</p><h6>To whom was it written?</h6><p>Believers in the city of Philippi, located in northeastern Greece. It lay ten miles inland from the modern port city of Kavalla.</p><h6>What led to the writing of this letter?</h6><p>The Roman colony of Philippi was where Paul planted the first church on European soil (Act 16:11-40), probably around AD 50. When Paul moved on, the church occasionally sent him financial aid, one of the few churches to do so (Phl 4:15). Paul wanted to thank the Philippians for sending him money to help defray his living expenses as he awaited trial (Phl 4:10-18). Paul also wanted to warn them against false teachers and urge them to greater unity among themselves.</p><h6>SourceView Insights</h6><p>This letter contains only Pauls side of the dialogue with his dear friends in Philippi; thus all the SourceView text is in black. To give the letter the original handwritten feel, it is displayed in a cursive script.</p>"},{"story":89,"bookId":50,"bookName":"Philippians","ref":"1:1-4:23","prevPath":"/388","path":"/389","nextPath":"/390","display":"block","title":"Rejoice!","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you discover any comparisons made by the author of this story? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you discover any contrasts made by the author of this story? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have found in this story.","Question4":"Please take time to write a short prayer to God responding to what you have discovered through these comparisons, contrasts and repetitions."},{"story":90,"bookId":51,"bookName":"Colossians","prevPath":"/389","path":"/390","nextPath":"/391","display":"none","title":"Introduction to Colossians","html":"<h6>What more could we possibly need?</h6><p>Paul reminds confused believers that Christ is everything</p><h6>How to read Colossians</h6><p>The best way to spot a counterfeit is to study the real thing. Paul shines the spotlight on the glory and majesty of Christ and the life hes given us as the best antidote to the toxic counterfeit teachings in the culture surrounding this young church. Jesuswho he is, what hes done, what its like to live in himare center stage. Paul uses the richest language to try to capture the amazing qualities of our extraordinary God!</p><p>In the first two chapters, Paul confronts heresy with a one-two punch. First, he declares that Jesus is not only the Creator and Sustainer of everything, but he is in fact the exact representation of God. Nothing less than all the fullness dwells in him (Col 1:17-19). Second, Paul exposes the false idea that philosophy and religiosity can successfully combat evil desires (Col 2:23). He reminds the Colossians that believers already have what they need for spiritual victory through union with Christ (Col 2:10).</p><p>In the last two chapters, Paul makes it clear: If you want to do the will of God, set your mind on Jesus and focus on the life we have received in him. As you read, be prepared! Your relationship with Jesus could be raised to a whole new level. As you acknowledge Jesus as Lord over your attitudes and actions and place your life under his rule, you will grow in holiness. Soak it all in! As you embrace him, your obedience and faith will take off! The message of the gospel is all true. All other substitutes pale in comparison to the real thing!</p><h6>Who wrote the book and when?</h6><p>The apostle Paul wrote it about AD 6062, while under house arrest in Rome, awaiting trial before the Roman emperor Nero. Not one to waste time, he took the opportunity to write and strengthen the church in Colosse, a city in the southwest of modern Turkey.</p><h6>How does it fit into Gods big story?</h6><p>Epaphras, a disciple of Paul, had founded the church at Colosse. The church was under constant pressure from competing religious groups. One heresy stated that the gospel was not enough; it was necessary to obtain secret knowledge if you wanted to be saved. Paul countered this supposedly secret (gnostic) sect and its hybrid of religion and philosophy that mixed Christian, Jewish, and pagan beliefs. He stated that the time of religious secrets is over! ... I was made a servant, according to the stewardship of God which was given me toward you, to fulfill the word of God,  the mystery which has been hidden for ages and generations. But now it has been revealed to his saints, to whom God was pleased to make known what are the riches of the glory of this mystery among the Gentiles, which is Christ in you, the hope of glory (Col 1:25-27).</p><h6>SourceView Insights</h6><p>This letter contains only Pauls side of the dialogue with believers in Colosse, so all the SourceView text is in black. A cursive script was used to display the letter to recreate the original handwritten feel of this personal document.</p>"},{"story":91,"bookId":51,"bookName":"Colossians","ref":"1:1-4:18","prevPath":"/390","path":"/391","nextPath":"/392","display":"block","title":"Fully God, Fully Human","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?"},{"story":92,"bookId":52,"bookName":"1 Thessalonians","prevPath":"/391","path":"/392","nextPath":"/393","display":"none","title":"Introduction to 1 Thessalonians","html":"<h6>Life and death issues</h6><p>Paul shows us how to face the future with faith and hope</p><h6>How to read 1 Thessalonians</h6><p>Seductive images and sexual pressures are everywhere in todays culture. It seems like the situation was not much different in Thessalonica. We face the same questions the early church did: How do we live holy lives in the midst of an immoral culture? As we struggle to maintain a pure life in a permissive society, 1 Thessalonians provides solid guidelines and an encouraging promise: He who calls you is faithful, who will also do it (1Th 5:24).</p><p>Pauls passionate, pastoral heart comes through in his joy over and deep concern for the Thessalonian church. His confidence lies in the power of Gods Word to transform them. Pay careful attention to the beautiful picture of discipleship in 1Th 2:8, as love compels you also to share the gospel and your life with those the Lord places in your path. Overshadowing and undergirding all this is the reality of seeing life through the lens of eternity. Look for instruction on how Jesus second coming might inspire you to face an uncertain future with great hope and motivate you to live a life pleasing to God.</p><h6>Who wrote this book and when?</h6><p>The apostle Paul wrote it, possibly as early as AD 50 or shortly thereafter.</p><h6>To whom was it written and why?</h6><p>Paul wrote to the believers in the church at Thessalonica, founded during his second missionary journey. Riots and opposition had forced him to leave them sooner than he desired. But later news of their progress encouraged Paul to write to commend them for growing in the Lord and to urge them to correct some misunderstandings.</p><h6>SourceView Insights</h6><p>All the SourceView text is black because this letter contains only one side of Pauls dialogue with the Thessalonian believers. The letter is displayed in a cursive script, recreating the original handwritten feel of this personal document.</p>"},{"story":93,"bookId":52,"bookName":"1 Thessalonians","ref":"1:1-5:28","prevPath":"/392","path":"/393","nextPath":"/394","display":"block","title":"Live Holy Awaiting His Return","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Meditate on the acts of God described in this portion of Scripture.","Question2":"What do these clarify about the heart of God?","Question3":"Have you experienced this expression of the heart of God in your life? If yes, how so? If not, what steps can you take to experience this?","Question4":"What can you do to grow in your desire to know and obey God more intimately in your life?"},{"story":94,"bookId":53,"bookName":"2 Thessalonians","prevPath":"/393","path":"/394","nextPath":"/395","display":"none","title":"Introduction to 2 Thessalonians","html":"<h6>Stick with it</h6><p>A message of Christs coming encourages believers to endure</p><h6>How to read 2 Thessalonians</h6><p>What is going on? At times the world around us seems chaotic and unpredictable. This letter provides something stablean eternal perspectivewith which to evaluate societys shifting standards. Pauls words remind us that as the world careens towards its final end, our hope of eternity strengthens us to live day-by-day in an anti-Christian environment.</p><p>Chapter 1 provides encouragement to Christians undergoing persecution to persevere in the midst of hardship. Chapters 2 and 3 confront false reports that the last days had already come and gone. These chapters reveal that certain things have to happen before Jesus returns, and they urge an unwavering trust that God will accomplish his plan of redemption. The church is encouraged to be patient and steady. We are to faithfully persevere in diligent service until Jesus comes again.</p><p>Youll notice several subjects that parallel those in Pauls first letter to the Thessalonians: suffering (1Th 2:14-16; 2Th 1:3-12); work (1Th 4:9-12; 5:14; 2Th 3:6-15); and the end times (1Th 4:13 5:11; 2Th 2:1-12). Together, these letters tell us much of what we know about the Lords return.</p><h6>Who wrote this book?</h6><p>The apostle Paul wrote it in the early AD 50s, shortly after he wrote 1 Thessalonians.</p><h6>To whom was it written and why?</h6><p>Paul wrote to believers at Thessalonica who needed an even stronger dose of the advice he had given in his first letter.</p><h6>SourceView Insights</h6><p>Reading Pauls letter to the Thessalonian believers is like listening to one side of a cell phone conversation. Because of that, all the SourceView text is in black. The letter is displayed with a cursive script to give it a handwritten feel and allow us to relive the experience of reading this divinely inspired letter.</p>"},{"story":95,"bookId":53,"bookName":"2 Thessalonians","ref":"1:1-3:18","prevPath":"/394","path":"/395","nextPath":"/396","display":"block","title":"Stand Strong in Hope","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you discover any comparisons made by the author of this story? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you discover any contrasts made by the author of this story? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have found in this story.","Question4":"Please take time to write a short prayer to God responding to what you have discovered through these comparisons, contrasts and repetitions."},{"story":96,"bookId":54,"bookName":"1 Timothy","prevPath":"/395","path":"/396","nextPath":"/397","display":"none","title":"Introduction to 1 Timothy","html":"<h6>When leadership gets tough</h6><p>Firm words to strengthen Timothy in his life and ministry</p><h6>How to read 1 Timothy</h6><p>While a solo performance can be amazing, theres a unique richness expressed by a symphony of many artists and instruments flowing together under a master conductor. Each of us has an instrument to play. Together in the church we create an incredible expression of who God is when we play in harmony with one another! What youre about to read is like a conductors handbook. It summarizes guidelines for running a church, offering practical help to believers in their relationships with each other, with church leaders, and with the world around them.</p><p>Paul is writing to a beloved friend who has accompanied him on many missionary ventures. His instructions for the church are interspersed with personal directives and encouragement to his protg Timothy. This combination results in a fabulous manual for corporate and personal discipleship.</p><p>This practical, nitty-gritty wisdom must be understood within the specific situation Paul was addressing. You might repeatedly ask yourself, I wonder what conditions in Ephesus prompted Paul to write that? Look for the underlying principles. It might help to imagine you are eavesdropping on the conversation between an older minister and his younger colleague. Although the specific problems and answers might never be exactly duplicated, the principles of the gospel never change.</p><h6>Who wrote this book and when?</h6><p>Paul, the apostle, wrote it sometime shortly after he was released from imprisonment in Rome, probably around AD 6365.</p><h6>To whom was it written and why?</h6><p>Paul wrote to Timothy with advice on how to guide the church at Ephesus. False teachers threatened to undermine Pauls work there, and Timothy, one of Pauls dear friends, was in a tough situation where he needed personal encouragement and advice on how to guide the church.</p><h6>SourceView Insights</h6><p>All the SourceView text is black because Pauls letter contains only his side of this written conversation with Timothy. The letter is displayed with a cursive script to give it a handwritten feel and allow us to relive the experience of reading this divinely inspired letter.</p>"},{"story":97,"bookId":54,"bookName":"1 Timothy","ref":"1:1-6:21","prevPath":"/396","path":"/397","nextPath":"/398","display":"block","title":"Mentoring a Young Leader","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?"},{"story":98,"bookId":55,"bookName":"2 Timothy","prevPath":"/397","path":"/398","nextPath":"/399","display":"none","title":"Introduction to 2 Timothy","html":"<h6>A letter from death row</h6><p>Pauls final words challenge Timothy to protect and pass on the truth</p><h6>How to read 2 Timothy</h6><p>The last words of a person facing imminent death often make a significant impact. This is certainly the case in Pauls last letter to his beloved disciple and faithful colleague! Paul, imprisoned in Rome for the second time, faced certain martyrdom. The clarity and power of these his final words not only served Timothy, his son in the faith, but have echoed through the hearts of believers around the world down through the ages. Certainly his dying words are words to live by!</p><p>Pauls passionate advice is just as vital for us as it was for Timothy back in his day: Guard the priceless giftthe pure gospel of salvation by grace alone; endure hardship; rely on Gods inspired Word; and stay focused on spreading the gospel.</p><p>Watch for pithy statements, as Paul attempts to pull together the wisdom of a lifetime of service to God. Note particularly the ways he challenged Timothy to a more effective ministry. The nuggets hidden here were forged in the crucible of life-threatening experiences, but they point to the hope that belongs to all of us in Jesus Christ.</p><h6>Who wrote this book and when?</h6><p>The apostle Paul wrote it to Timothy, probably in AD 66 or 67, while he was held in a Roman prison after the great fire of Rome. Although set by Nero, the fire became the trigger that launched the first major persecution of the church when they were unjustly blamed for the fire.</p><h6>How does it fit into the big story?</h6><p>Paul, imprisoned in Rome for the second time, realized he would not be released. Many of his supporters, perhaps thinking things were hopeless, had abandoned him in prison. As Neros crazed antics resulted in the death of thousands of believers, Paul knew that his remaining time on earth was short. His difficult circumstances, his concern for the churches hed planted, and his love for Timothy spurred him to write these final words.</p><h6>SourceView Insights</h6><p>All the SourceView text is black because Pauls letter contains only his side of this written conversation with Timothy. The letter is displayed with a cursive script to give it a handwritten feel and allow us to relive the experience of reading this divinely inspired letter.</p>"},{"story":99,"bookId":55,"bookName":"2 Timothy","ref":"1:1-4:22","prevPath":"/398","path":"/399","nextPath":"/400","display":"block","title":"An Old Mentor's Final Words","questionsTitle":"OPENING THE WORD","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?"},{"story":100,"bookId":56,"bookName":"Titus","prevPath":"/399","path":"/400","nextPath":"/401","display":"none","title":"Introduction to Titus","html":"<h6>The gospel in a nutshell</h6><p>Redeemed by grace for a life of good works</p><h6>How to read Titus</h6><p>This short letter shows that it has always been a challenge to develop a good churcheven in New Testament days. It tells Titus, a young leader of an argumentative church, to refute false teachers, do away with church disunity, and find quality leaders. Even with Gods Spirit at work, church life can involve a lot of sacrificial work! But Paul encourages Titus to persevere. He states, concerning these things I desire that you affirm confidently, so that those who have believed God may be careful to maintain good works. These things are good and profitable to men (Tit 3:8).</p><p>Paul intersperses doctrine and practice throughout this brief but powerful letter in a way that integrates eternal truth and day-to-day transformation. Look particularly at the testimony to Gods truthfulness in Tit 1:1-4. Then reflect upon his marvelous kindness and love and grace that not only redeems you but also motivates you to do what is good (Tit 2:11-14; 3:4-7). This is the essence of the gospel!</p><p>This book is a must for anyone who is eager to put the gospel into practice. In it youll find outlined the qualifications of church leaders, guidelines for a godly lifeincluding successful relationships between family, friends, and societyand an emphasis on faith that overcomes division and disharmony among believers.</p><h6>Who wrote this book and when?</h6><p>The letter begins with a reference to a joint missionary trip taken by Paul and Titus to establish the gospel among Jews and Gentiles. Because this trip is not recorded in Lukes account of Pauls travels, many suggest that it was written sometime after the events recorded in the books of history. According to this scenario, Paul would have written it sometime between AD 63 and 65, while traveling after his first release from a Roman prison.</p><p>Another alternative is possible: that Paul and Titus traveled together to Crete before the first missionary journey from Antioch (Act 13:1-14:28). This would have occurred during the fourteen mostly silent years after Pauls conversion (see Gal 1:15-2:10). In this scenario, he may have set out from Troas in Cilicia with a young Titus as his ministry companion. If this is the case, Titus could have been written in the AD 40s and is perhaps the earliest New Testament document.</p><h6>To whom was it written and why?</h6><p>To Titus, a close friend and protg of Paul (2Co 8:23), who helped Paul organize and lead churches in the eastern half of the Roman empire. Paul wrote these instructions to help Titus an emerging Gentile Christian leaderguide the young churches on the island of Crete.</p><h6>SourceView Insights</h6><p>Pauls letter to Titus is displayed with a cursive script to give it a handwritten feel. All the SourceView text is black because Pauls letter contains only his side of this written conversation with Titus.</p>   "},{"story":101,"bookId":56,"bookName":"Titus","ref":"1:1-3:15","prevPath":"/400","path":"/401","nextPath":"/402","display":"block","title":"The Essential Gospel","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given commands can you discover in this story?","Question2":"Put yourself in the story and imagine what it originally might have been like to receive this command. How does this relate to you in your personal life today?","Question3":"What God-inspired promises can you discover in this story? How can you walk in more of this in your own life?","Question4":"Draw a picture about what these promises mean for you."},{"story":102,"bookId":57,"bookName":"Philemon","prevPath":"/401","path":"/402","nextPath":"/403","display":"none","title":"Introduction to Philemon","html":"<h6>Be reconciled!</h6><p>Paying the price of forgiveness</p><h6>How to read Philemon</h6><p>Forgiveness is often tough. C.S. Lewis said, Everyone says that forgiveness is a wonderful idea, until he has something to forgive. Someone has to swallow the pain of having been hurt. Philemon is a letter that showcases the cost of asking for and of granting forgiveness.</p><p>Through Pauls ministry Onesimus had come to faith in Jesus while a runaway slave. In that day, most runaway slaves, if caught, faced harsh punishment and sometimes even death. Philemon, Onesimus master, was also a Christian and Pauls close friend. Paul sent Onesimus back to Philemon with this letter in hand in order that their broken relationship might be repairedfor they were now brothers in Christ.</p><p>This is the briefest of all of Pauls writings, and one of the most magnificent illustrations of grace and forgiveness in the Bible. Pauls deep concern for reconciliation calls us to apply these same principles of love and compassion to our own relationships. As we also stand in need of Gods grace and forgiveness, its a welcome declaration of the cleansing available to us all through Jesus, our mediator who has come to set us free with his offer of forgiveness!</p><h6>Who wrote this book and when?</h6><p>The apostle Paul wrote from prison in Rome about AD 6062.</p><h6>To whom was it written?</h6><p>Philemon, a wealthy Christian in the church at Colosse, who may have become a believer through Pauls ministry (verse 19).</p><h6>Why was it written?</h6><p>During Pauls first imprisonment in Rome (AD 5962), he met Onesimus, a runaway slave, and led him to faith in Jesus (verse 10). Onesimus might have been a great help to him, but Paul chose to send him back to his master, Philemon. Paul wrote this letter to ask Philemon to forgive his former slave.</p><h6>SourceView Insights</h6><p>Pauls letter to Philemon contains only his side of the dialogue, so all the SourceView text is in black. A cursive script was used to display the letter to recreate the original handwritten feel of this personal document.</p>"},{"story":103,"bookId":57,"bookName":"Philemon","ref":"1:1-1:25","prevPath":"/402","path":"/403","nextPath":"/404","display":"block","title":"Grace For a Runaway Slave","questionsTitle":"DIGGING DEEPER","Question1":"Why is Paul writing to Philemon?","Question2":"Who was Onesimus and how did Paul meet him?","Question3":"How did Paul expect the gospel to reshape the relationship between Philemon and Onesimus?","Question4":"Are there accepted relationships in your culture that the gospel would reshape? What would that look like?"},{"story":104,"bookId":58,"bookName":"Hebrews","prevPath":"/403","path":"/404","nextPath":"/405","display":"none","title":"Introduction to Hebrews","html":"<h6>Jesus is beyond compare</h6><p>He has made a way for you; follow himno matter what the cost</p><h6>How to read Hebrews</h6><p>We all face rough times. When the pressure is on, we may be tempted to give up. We need a reminder of what were contending for to get us through those tough spots. Hebrews was written for just such a situation. It was for Jewish followers of Jesus who were facing really severe persecution because of their faith. In light of the hardship, some had considered abandoning the faith altogether. The author exhorts them to keep going regardless of the price. Why?</p><p>The reason is simple. Nothingand no onecompares to Jesus!</p><p>He is better than the angels, better than Moses, better than Joshua, better than Aaron, better than Melchizedek. Indeed, he instituted a better covenant based on better promises resulting in a better rest because hes made a better sacrificegiving his very own life. In fact, if you were to consider all the great heroes of the faith, Jesus is better yet! That is why we must consider ... Jesus (Heb 3:1) and keep looking to Jesus him and him alone (Heb 12:2). He is better than betterhe is the best there is! The stirring conclusion is clear: Because Jesus is beyond compare, it is worth following him no matter what it costs!</p><p>The writer makes frequent contrasts between Old Testament ritual law and New Testament faith. Notice the strong appeals made to persuade the Jewish believers to stick with the new covenant rather than going back to the old. Look for the vivid Old Testament images used to illustrate what God has done through Jesus Christ. Read the stories of those who endured through the Old Testament, clinging to faith and grace in anticipation of the Christ, desire a better country, that is, a heavenly one (Heb 11:16). Delight in the fabulous descriptions of the incomparable life and nature of our great Messiah. And determine in your heart to treasure Jesus above all elsethats when you know youve really got the message of Hebrews!</p><h6>Who wrote this book?</h6><p>This is a debated question. The answer is unclear because the author does not identify him or herself. Though some have suggested it was written by Paul, it is more likely written by one of his trusted Jewish associates: Barnabas, Apollos, or Priscilla. The fact that the document remained anonymous may point strongly to the female authorship of Priscilla.</p><h6>When was it written?</h6><p>The nature of the persecution described in the letter indicates a time in which there was no social stigma for being a Jew, but there was for being a follower of Jesus. The author says, partly, being exposed to both reproaches and oppressions; and partly, becoming partakers with those who were treated so. For you both had compassion on me in my chains, and joyfully accepted the plundering of your possessions, knowing that you have for yourselves a better possession and an enduring one in the heavens (Heb 10:33-34). This could indicate the time of persecution under Emperor Nero, perhaps after Peter and Pauls martyrdom, sometime in the late AD 60s. Note that TimothyPauls close companionhad just been freed (Heb 13:23).</p><h6>To whom was it written and why?</h6><p>Are there times when you would prefer not to identify yourself with Christ because of social pressures and cultural perceptions? Then you may be able to understand somewhat the dilemma facing these early Jewish believers. Because of the particular nature of the persecution they were experiencing, it was safe to be known as a Jew, but it was potentially life-threatening to be identified as a Christian. Therefore, Hebrews sounded a warning to these early Jewish believers who were being tempted to revert to their Old Testament rituals in an attempt to escape persecution and save their families, their businesses, their lives. Hebrews exhorts them that the new and life-giving way of faith in Jesus is better! (Heb 10:20).</p><h6>SourceView Insights</h6><p>Though sometimes it is referred to as an epistle, Hebrews is actually not a letter. It does not contain the expected elements of a first-century letter. It does not state who wrote it nor to whom it was written. It contains no initial greeting or prayer of blessing. Instead, it refers to itself as a word of exhortation (Heb 13:22) for it takes only 45 minutes to read. Apparently then, what we have before us is the transcript of an inspired sermon. But this is not just a religious message. It is more like a political rally!</p><p>This exhortation patterns itself after a form of political speech common in the Roman era known as a synkrisis. In this genre of speech, the advocate of a candidate would deliver a message comparing the candidate with famous persons in the citys past. In every instance the candidate would be declared to be better than the predecessor. If the political speech was effective, the hearers would break into enthusiastic partisan cheers, chanting loudly the name of the candidate for whom they would vote! In the same way the author of Hebrews lifted up Jesus and expected the hearers to end proclaiming our allegiance to his name (Heb 13:15), aligning themselves unswervingly with Jesuseven when facing persecution!</p><p>At the same time, the author conveys a deep caring, pastoral concern for her or his fellow-believers who are going through these rough times. The author identifies with the hearers and places the words of Psalm 118:6 into their own mouths, saying, So that with good courage we say, The Lord is my helper. I will not fear. What can man do to me? (Heb 13:6).</p><p>Time and again the author empathizes with the persecuted believers, using first person plural language (we, our, ours, us) to identify with these original hearers of this synkrisis. Consider what is written in 1:1, 2, 3; 2:1,3, 5, 8, 9, 16, 17, 18; 3:1, 6, 14, 19; 4:1, 2, 3, 4, 11, 12, 13, 14, 15, 16; 5:11; 6:1, 3, 9, 11, 18, 19, 20; 7:8, 9, 13, 14, 26; 8:1, 3, 6; 9:5, 12, 14, 24, 28; 10:10, 12, 19, 21-26, 29, 30, 39; 11:1, 3, 4, 40; 12:1, 2, 9, 10, 25, 28, 29; 13:10, 13, 14, 15, 18, 20, 23. The pastoral empathy exhibited in these passages should be an inspiration for us to live our lives in a similar way: identifying closely with those who are undergoing persecution for their faith in the amazing person of Jesus Christ.</p>"},{"story":105,"bookId":58,"bookName":"Hebrews","ref":"1:1-4:13","prevPath":"/404","path":"/405","nextPath":"/406","display":"block","title":"Better than Angels, Moses & Joshua","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the many questions raised in this passage. Who makes them? To whom are they made?","Question2":"Is there a question that impacts you more than others? Why?","Question3":"What questions would do you have about issues raised in this passage?","Question4":"What can you do to get some answers to these questions you have raised?"},{"story":106,"bookId":58,"bookName":"Hebrews","ref":"4:14-7:28","prevPath":"/405","path":"/406","nextPath":"/407","display":"block","title":"Better than Melchizedek, Aaron & Priests","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this portion of Scripture? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this portion of Scripture?","Question3":"What do you think God was wanting accomplish through the life of your selected character?","Question4":"What do you think God is wanting to accomplish through your life? How can you cooperate with his kind purposes in your life?"},{"story":107,"bookId":58,"bookName":"Hebrews","ref":"8:1-10:18","prevPath":"/406","path":"/407","nextPath":"/408","display":"block","title":"A Better Covenant, Promise & Sacrifice","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given commands can you discover in this story?","Question2":"Put yourself in the story and imagine what it originally might have been like to receive this command. How does this relate to you in your personal life today?","Question3":"What God-inspired promises can you discover in this story? How can you walk in more of this in your own life?","Question4":"Draw a picture about what these promises mean for you."},{"story":108,"bookId":58,"bookName":"Hebrews","ref":"10:19-12:13","prevPath":"/407","path":"/408","nextPath":"/409","display":"block","title":"Better Than All Heroes of the Faith","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this passage? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this passage?","Question3":"What do you think God was wanting do in the life of your selected character?","Question4":"What do you think God is wanting to do in your life as you reflect on this passage? How can you cooperate with his loving purposes this week?"},{"story":109,"bookId":58,"bookName":"Hebrews","ref":"12:14-13:25","prevPath":"/408","path":"/409","nextPath":"/410","display":"block","title":"Jesus' Way is Better","questionsTitle":"OPENING THE WORD","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?"},{"story":110,"bookId":59,"bookName":"James","prevPath":"/409","path":"/410","nextPath":"/411","display":"none","title":"Introduction to James","html":"<h6>Faith in action</h6><p>Walk the talk: Live like you believe it!</p><h6>How to read James</h6><p>Tired of spin? Want someone to tell it to you straight? James is the one to do it! He cuts straight to the quick. Hes not trying to please anyone but the Lord in this plainspoken letter to believers everywhere. His message clearly exposes wrong motives and wrong actions. Packing this brief letter with 54 commands, he instructs us how to live a life of faith that is Pure ... and undefiled (Jam 1:27). Many of Jamess statements echo the proverbs of the Old Testament and Jesus teaching in the Sermon on the Mount (Mat 5-7). He urges us to respond with love, humility, and patience, no matter what the circumstances of our day-to-day life. This is true faith.</p><p>This letter takes a no-nonsense approach to combat hypocrisy and religiosity. James describes the evil of a tongue out of control, of playing favorites with the rich, or of prideful boasting about self-made plans. Dont look for pious platitudes here. Expect a string of hard-hitting, specific, practical instructions to help you live an authentic Christian life.</p><h6>Who wrote this book?</h6><p>Most probably James, Jesus half-brother (Gal 1:19) and leading elder of the church in Jerusalem (Act 15:13-21). This letter may have been one of the first New Testament books to be written between AD 40 and 50.</p><h6>Why was it written?</h6><p>To teach believers that faith and good deeds go hand-in-hand in the Christian walk (Jam 2:14-20)one without the other is lifeless. His message is simple but challenging: If you believe in Jesus, do as Jesus did!</p><h6>SourceView Insights</h6><p>Reading Jamess letter is like listening to one side of a telephone conversation. Because of that, all the SourceView text is in black. To help us relive the experience of reading these divinely-inspired letters, the letter is displayed with a cursive script to give it a handwritten feel.</p>"},{"story":111,"bookId":59,"bookName":"James","ref":"1:1-5:20","prevPath":"/410","path":"/411","nextPath":"/412","display":"block","title":"Faith Works","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?"},{"story":112,"bookId":60,"bookName":"1 Peter","prevPath":"/411","path":"/412","nextPath":"/413","display":"none","title":"Introduction to 1 Peter","html":"<h6>What to do in times of trouble</h6><p>You are royalty! So dont give up!</p><h6>How to read 1 Peter</h6><p>Where do you find your hope in times of crises? Where can you find the strength to carry on in hard times? What will help you grow in faith when difficulties abound? The answer? Jesus! Because of what he has done you have been given a new identity. You now belong to God, and form part of a chosen race, a royal priesthood, a holy nation, a people for Gods own possession (1Pe 2:9).</p><p>So what about those tough times? God can and will use difficulties to strengthen us. Discover how faith, refined by suffering, can help you see the Lord more clearly and get to know him more dearly. The words of this letter encourage us to hold firm in difficult times.</p><p>But Peter did not stop there. Its not just a matter of survival. As we learn to embrace our royal identity, we can see the kingdom of God groweven in the face of adversity. Peter urges us to adopt an on-the-offensive attitude in the midst of difficulty, and proclaim the excellence of him who called you out of darkness into his marvelous light (1Pe 2:9).</p><h6>Who wrote this book and why?</h6><p>Sometime in the early to mid AD 60s, Peterone of the original twelve apostleswrote it from Rome to believers scattered throughout the regions of Asia Minor, in what is now Turkey. Peter saw that increasing hardship and persecution had caused some Christians to wonder if God had abandoned them. He wrote to encourage them by offering hope and meaning in the midst of their suffering.</p><h6>Where does it fit into Gods big story?</h6><p>At first the Roman government had given Christians the same freedom of religion as the Jews. As the rift between Jews and Christians grew, tolerance for Christianity faded. Because Roman policy was to ban problem religions perceived as a threat to the empires stability, Christians began facing discrimination, violence, arrest, and confiscation of property. Some were beginning to waver in their faith. Because Peter had himself been imprisoned and beaten for his faith, he could address the subject of suffering with genuine authority.</p><h6>SourceView Insights</h6><p>Peters letter to encourage suffering believers is one side of an on-going conversation; thus all SourceView text is black. The letter is displayed with a cursive script to give it a handwritten feel and allow us to relive the experience of reading this divinely inspired letter.</p>"},{"story":113,"bookId":60,"bookName":"1 Peter","ref":"1:1-5:14","prevPath":"/412","path":"/413","nextPath":"/414","display":"block","title":"Do Right No Matter What","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the many questions raised in this passage. Who makes them? To whom are they made?","Question2":"Is there a question that impacts you more than others? Why?","Question3":"What questions would do you have about issues raised in this passage?","Question4":"What can you do to get some answers to these questions you have raised?"},{"story":114,"bookId":61,"bookName":"2 Peter","prevPath":"/413","path":"/414","nextPath":"/415","display":"none","title":"Introduction to 2 Peter","html":"<h6>Witnesses of his majesty</h6><p>Get ready, because Jesus will come!</p><h6>How to read 2 Peter</h6><p>Information overload! We can be swamped with a barrage of information. How do we sort it all out? How do we tell which information is importantor true? The danger of receiving false information existed in the church in Peters dayas in ours. Thats why the message of 2 Peter is so critical today.</p><p>Peter confronts those spreading doubts about Jesus deity with great personal authority, for he himself was an eyewitness to his majesty (2Pe 1:16). Jesus is who he says he is! He is the glorious son of God. You can take him at his word!</p><p>As a result Peter challenges believers to resist the temptation to compromise morally or to turn their backs on the gospel. He also counsels us to pursue a godly lifestyle anticipating Jesus return. Peter tells us that we can hurry along the day of Jesus coming (2Pe 3:12) by actively sharing our faith with others. The reason God has delayed his return is to give us an opportunity to draw more people into his kingdom, for he does not want any should perish, but that all should come to repentance (2Pe 3:9). This is Gods deepest desire, his most heartfelt longing. We can bring great joy to his heart as we lead others into living relationship with him.</p><h6>Who wrote this letter and when?</h6><p>The apostle Peter wrote it most likely from Rome, sometime between AD 64 and 68.</p><h6>To whom was this letter written and why?</h6><p>To the same people who received his first letter: believers scattered throughout the regions of Asia Minor, in what is present-day Turkey. Peter wrote to warn them about false teachers and strengthen them with the truth.</p><h6>What was the historical context?</h6><p>Emperor Nero was on the throne in Rome at the time Peter wrote this letter. Nero was a man without moral principles. His volatile nature created a cruel and unstable political climate which threatened both the empire and the young church. At the same time, Christians in Asia Minor were in danger of being led astray by eloquent but erring teachers who had infiltrated their fellowship.</p><h6>SourceView Insights</h6><p>All the SourceView text is black since Peters letter encouraging believers to stand firm in their faith reflects just one side of a two-sided conversation. The letter is displayed with a cursive script, giving it a handwritten feel.</p>"},{"story":115,"bookId":61,"bookName":"2 Peter","ref":"1:1-3:18","prevPath":"/414","path":"/415","nextPath":"/416","display":"block","title":"Live Godly Lives","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you describe any comparisons made by the author of this portion of Scripture? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you describe any contrasts made by the author of this story portion of Scripture? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have observed in this portion of Scripture.","Question4":"Please take time to post on your social media what you have learned through this passage."},{"story":116,"bookId":62,"bookName":"1 John","prevPath":"/415","path":"/416","nextPath":"/417","display":"none","title":"Introduction to 1 John","html":"<h6>Genuine fellowship with God</h6><p>Live like youre loved!</p><h6>How to read 1 John</h6><p>The beloved disciple who enjoyed intimate friendship with Jesus on the earth, has authority like none other to assure us of Gods love. His short letter is packed with powerful insights about the love of God. He calls us to live like were loved in our relationships with God and man. The key? Getting to know God more and more. Intimate revelation of his loving character is the foundation for transformation. John tells us how great a love the Father has given to us and then affirms that we know that, when he is revealed, we will be like him; for we will see him just as he is. Everyone who has this hope set on him purifies himself, even as he is pure. (1Jo 3:1-3).</p><p>What does it look like to live life as Gods beloved? John describes the security, purity, humility, and sacrificial service to others that flows from a genuine, personal experience of Gods affection and commitment for you! John presents you with the wonderful fellowship you can have with God, confident that his but perfect love casts out fear (1Jo 4:18). This will equip you to live right by maintaining fellowship with the Lord.</p><p>John weaves together several core elements (such as light, love, life, truth, and sin) in a beautiful, multi-faceted work of art. You can inspect it up close, or stand back to take in the whole thing at once, and you always see some new combination of the colors and themes. So take in the big picture as you read it through all at once. Pick up one section at a time to study it intently. You can come back again and again, always catching new glimpses of Gods mind and heart that will flood you with courage, faith, and deep affection!</p><h6>Who wrote this book and when?</h6><p>The apostle John wrote it, probably late in the AD 80s, toward the end of his life. This same man also wrote the gospel of John.</p><h6>To whom was it written and why?</h6><p>John wrote to encourage and strengthen the believers in a group of churches near Ephesus in the western half of what today is the country of Turkey.</p><h6>What was happening at the time?</h6><p>Many communities were springing up throughout the Roman Empire, and a loose structure of authority and organization was growing. The first great persecution under Nero had claimed the lives of thousands of Christians, including Paul and Peter. As the last surviving apostle, John was the anchor for the soul of the early church in these stormy times.</p><h6>SourceView Insights</h6><p>Reading Johns letter is like listening to one side of a telephone conversation. Because of that, all the SourceView text is in black. To help us relive the experience of reading these divinely-inspired letters, the letter is displayed with a cursive script to give it a handwritten feel.</p>"},{"story":117,"bookId":62,"bookName":"1 John","ref":"1:1-5:21","prevPath":"/416","path":"/417","nextPath":"/418","display":"block","title":"Eternal Life Through Jesus","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?"},{"story":118,"bookId":63,"bookName":"2 John","prevPath":"/417","path":"/418","nextPath":"/419","display":"none","title":"Introduction to 2 John","html":"<h6>Close the door</h6><p>When not to put out a welcome sign</p><h6>How to read 2 John</h6><p>John tells his readers to be on the guard, to watch (verse 8) against that which undermines the community of faith. A life of love does not mean living without discernment. John clearly tells believers that if someone doesnt bring this teaching, dont receive him into your house, and dont welcome him (verse 10).</p><p>Read this letter as if you received it from a beloved, retired pastor whom you greatly respect. In it he is telling you how to respond to those whose words and deeds contradict the Word of Godthough they present themselves as his messengers. Look for the encouragement to know Gods truth and to walk in this truth without faltering. Be confident that genuine love stands for the truth and does not facilitate the spread of falsehood.</p><h6>Who wrote this letter and when?</h6><p>The apostle John, who also wrote 1 John and the gospel that bears his name, wrote it at roughly the same time as 1 John in the late AD 80s.</p><h6>To whom was it written and why?</h6><p>John wrote this personal note to Christians who may have felt manipulated by false teachers. Now as an old man, he writes to try to protect his beloved children in the faith from being deceived away from the truth of the gospel.</p><h6>SourceView Insights</h6><p>Johns letter to protect believers from false teachers is only one side of a two-way dialogue; thus all SourceView text is black. The letter is displayed with a cursive script to give it a handwritten feel and allow us to relive the experience of reading this divinely inspired letter.</p>"},{"story":119,"bookId":63,"bookName":"2 John","ref":"1:1-1:13","prevPath":"/418","path":"/419","nextPath":"/420","display":"block","title":"Close the Door","questionsTitle":"DIGGING DEEPER","Question1":"About whom does John issue a warning?","Question2":"How does John encourage the church to guard against deception?","Question3":"Are there people that you should not be listening to?","Question4":"How can you abide in the teaching of Christ?"},{"story":120,"bookId":64,"bookName":"3 John","prevPath":"/419","path":"/420","nextPath":"/421","display":"none","title":"Introduction to 3 John","html":"<h6>Open the door</h6><p>Put out the welcome sign!</p><h6>How to read 3 John</h6><p>Hospitality is important to God. He is the most hospitable being in the universe! His arms are always open wide to welcome all who want to join his family. He invites us to join him in this kingdom ministry. Johns words help us to see hospitality as a powerful, practical expression of the love of God. Johns words will also lead you to value the many diverse gifts at work within the church and encourage you to seek unity within the body of Christ.</p><p>This letter is an important companion piece to 2 John. In the earlier letter the apostle warned believers against supporting false teachers. In this one, he encourages believers to warmly welcome and strongly support those who do bring the truth of God to us. Taken together, these two letters focus us on the incredible value of the truth of the gospel and the need for both love and discernment. It will challenge you to consider how you can express Gods hospitality to others more fully.</p><h6>Who wrote this letter?</h6><p>The apostle John, who also wrote the other two letters and the gospel that bears his name. He probably wrote it in the late AD 80s.</p><h6>To whom was it written and why?</h6><p>John wrote this brief letter to his friend Gaius, perhaps to accompany his more general letter (1 John). He wanted to encourage him for his faithful support of legitimate teachers. He also was warning him about the actions of a certain strong-willed leader named Diotrephes.</p><h6>SourceView Insights</h6><p>Reading Johns letter to his friend may feel like eavesdropping. Because we cant hear Gaius response, all SourceView text here is black. The letter is displayed with a cursive script to give it a handwritten feel.</p><p>JUDE</p><h6>Contend for the faith</h6><p>When evil abounds, hold on to God because he holds on to you</p><h6>How to read Jude</h6><p>Ever disconnect the smoke detector or the seatbelt buzzer because you didnt want to be bothered with the irritating noise? Ignoring such warnings could cost you your home or your life. But ignoring spiritual warnings could be even worse. Thats why we should pay close attention to this book. Jude sounds a short sharp warning siren we dare not ignore.</p><p>Jude doesnt waste words as he describes whats wrong and what to do about it. He makes it crystal clear that weve got to take a stand for the truth and contend for the faith. As you read, you might want to flip back and forth with 2 Peter. Observe how many parallels there are between these two books as they both seek to combat false teaching in the church.</p><p>As you read Judes compelling words, be attentive to the warnings, but also notice the promises and the encouragement to live genuine Christian lives. You would think that with the need to contend against so much false teaching, Jude would be somewhat discouraged. Just the opposite is true! Hes more impressed with Gods capacity to keep us in his ways than with the enemys attempts to divert us. His final song of praise exudes great joy and confidence: Now to him who is able to keep them from stumbling, and to present you faultless before the presence of his glory in great joy, 25 to God our Savior, who alone is wise, be glory and majesty, dominion and power, both now and forever. Amen (verses 24-25).</p><h6>Who wrote this book and when?</h6><p>Jude was the half-brother of Jesus and full brother of James (author of James). He may have written this letter around the time Peter wrote his second letter, between AD 60 and 65. These two letters have striking similarities.</p><h6>Why was it written?</h6><p>Jude was concerned that Christians might be drawn away from the truth by slippery teachers of false doctrine. He wrote to urge believers to contend earnestly for the faith (verse 3).</p><h6>What was happening at the time?</h6><p>Impostors had begun to teach things contrary to the gospel and were causing confusion in the church. They taught it really didnt matter how people lived if they had been saved by grace. You can imagine the disaster this could produce! Jude wanted to make sure that no believer followed the example of Cain (Gen 4:1-25), Balaam (Num 22:1-24:25), or Korah (Num 16:1-49).</p><h6>SourceView Insights</h6><p>Reading Judes letter is like listening to a cell phone conversationone-sided. Therefore, all the SourceView text is in black. We have displayed the letter with a cursive script to recreate the original handwritten feel.</p>   "},{"story":121,"bookId":64,"bookName":"3 John","ref":"1:1-1:14","prevPath":"/420","path":"/421","nextPath":"/422","display":"block","title":"Open the Door","questionsTitle":"DIGGING DEEPER","Question1":"Whom does John say we should welcome in our midst?","Question2":"How does John encourage the church to support ministers of the gospel?","Question3":"Are there people that you should be listening to?","Question4":"How can you promote the teaching of Christ?"},{"story":122,"bookId":65,"bookName":"Jude","prevPath":"/421","path":"/422","nextPath":"/423","display":"none","title":"Introduction to Jude","html":"<h6>Contend for the faith</h6><p>When evil abounds, hold on to God because he holds on to you</p><h6>How to read Jude</h6><p>Ever disconnect the smoke detector or the seatbelt buzzer because you didnt want to be bothered with the irritating noise? Ignoring such warnings could cost you your home or your life. But ignoring spiritual warnings could be even worse. Thats why we should pay close attention to this book. Jude sounds a short sharp warning siren we dare not ignore.<br />   Jude doesnt waste words as he describes whats wrong and what to do about it. He makes it crystal clear that weve got to take a stand for the truth and contend for the faith. As you read, you might want to flip back and forth with 2 Peter. Observe how many parallels there are between these two books as they both seek to combat false teaching in the church.<br />   As you read Judes compelling words, be attentive to the warnings, but also notice the promises and the encouragement to live genuine Christian lives. You would think that with the need to contend against so much false teaching, Jude would be somewhat discouraged. Just the opposite is true! Hes more impressed with Gods capacity to keep us in his ways than with the enemys attempts to divert us. His final song of praise exudes great joy and confidence: Now all glory to God, who is able to keep you from falling away and will bring you with great joy into his glorious presence without a single fault. All glory to him who alone is God, our Savior through Jesus Christ our Lord. All glory, majesty, power, and authority are his before all time, and in the present, and beyond all time! Amen (verses 24-25).</p><h6>Who wrote this book and when?</h6><p>Jude was the half-brother of Jesus and full brother of James (author of James). He may have written this letter around the time Peter wrote his second letter, between AD 60 and 65. These two letters have striking similarities.</p><h6>Why was it written?</h6><p>Jude was concerned that Christians might be drawn away from the truth by slippery teachers of false doctrine. He wrote to urge believers to defend the faith (verse 3).</p><h6>What was happening at the time?</h6><p>Impostors had begun to teach things contrary to the gospel and were causing confusion in the church. They taught it really didnt matter how people lived if they had been saved by grace. You can imagine the disaster this could produce! Jude wanted to make sure that no believer followed the example of Cain (Gen 4:1-25), Balaam (Num 22:1-24:25), or Korah (Num 16:1-49).</p><h6>SourceView Insights</h6><p>Reading Judes letter is like listening to a cell phone conversationone-sided. Therefore, all the SourceView text is in black. We have displayed the letter with a cursive script to recreate the original handwritten feel.</p>"},{"story":123,"bookId":65,"bookName":"Jude","ref":"1:1-1:25","prevPath":"/422","path":"/423","nextPath":"/424","display":"block","title":"Holy Faith","questionsTitle":"FOLLOWING GODLY EXAMPLES","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?"},{"story":124,"bookId":66,"bookName":"Revelation","prevPath":"/423","path":"/424","nextPath":"/425","display":"none","title":"Introduction to Revelation","html":"<h6>Jesus victory and glory</h6><p>A call to overcome because our God reigns!</p><h6>How to read Revelation</h6><p>When the story gets so bleak and the bad guys are winning, its a relief to sneak a peek at the end of the bookthe good guys DO win in the end! Revelation reminds us that in the end justice and mercy triumph over evil, and every sorrow is comforted. Because Jesus has won the victory over sin and death, we are exhorted to live lives consistent with Gods coming kingdom.</p><p>This great prophecy is the climactic book of the New Testament. The four gospels describe Jesus life on earth. The many letters describe the ministry of the resurrected Christ. Revelation presents Jesus Christ as the glorious coming King who deserves your love, worship, and total allegiance. The assurance of his ultimate victory gives each of us courage to persevere in the midst of lifes challenges. Our confidence lies in the hope that the world will become the Kingdom of our Lord, and of his Christ. He will reign forever and ever! (Rev 11:15).</p><h6>Who wrote this book?</h6><p>Probably John, the apostle who also wrote the gospel of John and the three letters that bear his name.</p><h6>When was it written?</h6><p>Several dates are suggested. The most favored is AD 9096, near the end of the reign of the Roman emperor Domitian, about the time his persecution of the church began. These dark days required the light of hope of a victorious future.</p><h6>To whom was it written and why?</h6><p>This book went to seven churches in the Roman province of Asia (present-day Turkey) to warn them against falling away from their faith in Jesus Christ. It also offered them assurance of ultimate victory through the King of Kings and Lord of Lords who has the future firmly in hand.</p><h6>SourceView Insights</h6><p>Revelation certainly has an interesting speaking cast! John speaks through The Narrator (in black61.6 percent) and as himself (in greenonly 0.1 percent). God has much to say (in red21.2 percent), but perhaps the most intriguing part is that comprised of the other 36 cast members (in blue17.1 percent). Like Zechariah in the Old Testament we listen to the words of an extraordinary collection of out-of-this-world beings:</p><ul>  <li>We hear the voices of a wide assortment of celestial creatures (24 Elders, One of the 24 Elders, Four Living Beings, First Living Being, Second Living Being, Third Living Being, Fourth Living Being, One of the Seven Angels, A Third Angel, A Seventh Angel, A First Angel Flying, A Second Angel Flying, A Third Angel Flying, A Guiding Angel, A Mighty Angel Standing, A Strong Angel, An Angel from the East, An Angel from the Temple, An Angel in the Sun, An Angel with a Boulder, An Angel with a Sickle, An Angel with Authority, Millions of Angels, All the Angels, andinterestinglyAn Eagle).</li><br /><li>We also hear the words of the many redeemed (All Who Hear, All the Redeemed, All the Martyrs, All People, Every Creature, and A Vast Heavenly Crowd).</li><br /><li>And, sadly, we hear the voices of a few who refused to embrace the kingdom of God (The Worlds Merchants, The Worlds Shippers, and The Worlds Rulers).</li><br /><li>It is worthy of note that neither Satan nor his demonic hosts get a word in edgewise!</li></ul><p>You might still be approaching this book with some apprehension. Perhaps you are thinking, There are so many mysterious things in it, how can I possibly understand it? While many of the images are indeed unusual, there is much that is easily comprehensible in Revelation. Some of the first words of The Narrator and some of the final words spoken by God point us in the direction of increased understanding. Note the similarity between these two:</p><ul>  <li>Blessed is he who reads and those who hear the words of the prophecy, and keep the things that are written in it, for the time is at hand (1:3).</li><br /><li>Behold, I come quickly. Blessed is he who keeps the words of the prophecy of this book (22:7).</li></ul><p>Apparently the author thought that the main ideas were clear enough that the reader could readily understand and apply them. What is there to obey in this book? Well, there are over 120 imperatives throughout the book. The Narrator gives some clear commands (like come ... take the water of life freely in Rev 22:17) as do a variety of angels, including A First Angel Flying (Fear the Lord ... give him glory  Worship him ... in Rev 14:7) and A Guiding Angel (dont do it! I am a fellow bondservant ... Worship God ... in Rev 19:10 and again in 22:9).</p><p>But the greatest number of imperatives come directly from the mouth of God and are found in the red text. In fact 20 of the 28 red passages in Revelation contain clear commands for us to obey (Can you find them all?). The greatest number are concentrated in Rev 1:17b-3:22: Dont be afraid ... write  write  remember ... repent  do the first works  hear what the Spirit says ... write ... dont be afraid ... be faithful ... hear what the Spirit says ... write ... repent ... hear what the Spirit says ... write ... hold that which you have ... hear what the Spirit says ... write ... wake up ... keep the things that remain ... remember ... keep it ... repent ... hear what the Spirit says ... write ... Hold firmly that which you have ... hear what the Spirit ... write ... buy from me gold ... white garments ... and eye salve to anoint your eyes ... Be zealous  repent ... hear what the Spirit says ...</p><p>The repetition is purposeful. Are the key themes starting to emerge for you? Dont be afraid ... wake up ... repent ... keep ... hear what the Spirit says ...</p><p>If we go back to (1:3) and (22:7), we realize that these two passages not only speak of obeying the message of this book, but also of the blessings that accompany that act of obedience. There are another five passages in Revelation that also speak of being blessed. They state:</p><ul>  <li>Blessed are the dead who die in the Lord from now on. Yes, says the Spirit, that they may rest from their labors; for their works follow with them (14:13)</li><br /><li>Blessed is he who watches, and keeps his clothes, so that he doesnt walk naked, and they see his shame (16:15)</li><br /><li>Blessed are those who are invited to the marriage supper of the Lamb (19:9)</li><br /><li>Blessed and holy is he who has part in the first resurrection (20:6a)</li><br /><li>Blessed are those who do his commandments, that they may have the right to the tree of life, and may enter in by the gates into the city (22:14)</li></ul>"},{"story":125,"bookId":66,"bookName":"Revelation","ref":"1:1-3:22","prevPath":"/424","path":"/425","nextPath":"/426","display":"block","title":"Seven Churches","questionsTitle":"COMMANDS & PROMISES","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today."},{"story":126,"bookId":66,"bookName":"Revelation","ref":"4:1-8:1","prevPath":"/425","path":"/426","nextPath":"/427","display":"block","title":"Seven Seals","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?"},{"story":127,"bookId":66,"bookName":"Revelation","ref":"8:2-11:18","prevPath":"/426","path":"/427","nextPath":"/428","display":"block","title":"Seven Trumpets","questionsTitle":"LEARNING FROM THE PAST","Question1":"With which character do you most identify in this passage? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this passage?","Question3":"What do you think God was wanting do in the life of your selected character?","Question4":"What do you think God is wanting to do in your life as you reflect on this passage? How can you cooperate with his loving purposes this week?"},{"story":128,"bookId":66,"bookName":"Revelation","ref":"11:19-14:20","prevPath":"/427","path":"/428","nextPath":"/429","display":"block","title":"Endurance Amidst Hardship","questionsTitle":"OPENING THE WORD","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?"},{"story":129,"bookId":66,"bookName":"Revelation","ref":"15:1-16:21","prevPath":"/428","path":"/429","nextPath":"/430","display":"block","title":"Seven Bowls","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?"},{"story":130,"bookId":66,"bookName":"Revelation","ref":"17:1-20:3","prevPath":"/429","path":"/430","nextPath":"/431","display":"block","title":"The Sinful City is Fallen","questionsTitle":"COMPARISON, CONTRAST & REPETITION","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you see any comparisons made by the author of this passage? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you see, describe any contrasts made by the author of this passage? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have noted in this passage.","Question4":"Please take time to tell a friend what God is teaching you through this reflection on the Bible."},{"story":131,"bookId":66,"bookName":"Revelation","ref":"20:4-22:21","prevPath":"/430","path":"/431","nextPath":"/","display":"block","title":"The Holy City is Coming","questionsTitle":"HUNGERING TO KNOW GOD MORE","Question1":"Meditate on the acts of God described in this portion of Scripture.","Question2":"What do these clarify about the heart of God?","Question3":"Have you experienced this expression of the heart of God in your life? If yes, how so? If not, what steps can you take to experience this?","Question4":"What can you do to grow in your desire to know and obey God more intimately in your life?"}]

